/******************************************************************
MODAL STYLES
Styles for pop-up modal components
******************************************************************/

.modal {
    display: none;
    //display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0; right: 0;
    top: 0; bottom: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    background: rgba($color-white, 0.9);
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10;

    &__inner {
        display: block;
        margin: auto;
        position: relative;
        overflow-x: hidden;
    }

    &__close {
        position: absolute;
        top: 0; right: 0;
        width: 60px;
        height: 60px;
        padding: 20px;
        margin: 0;
        transition: $transition-default;

        &:hover {
            opacity: 0.6;
            transition: $transition-default;
        }
    }

    &__icon {
        display: block;
        width: 100%;
        height: 100%;
    }

    &.is-open {
        display: flex;
    }
}

.popup {
    display: none;
    //display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0; right: 0;
    top: 0; bottom: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    background: rgba($color-black, 0.8);
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 10;

    &__open {
        display: block;
        position: absolute;
        z-index: 99;
    }

    &__inner {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        background-color: $color-white;
        width: 90%;
        max-width: 960px;
        min-height: 515px;
        align-items: center;
        box-shadow: $shadow01;
        display: flex;
        flex-direction: row;
    }

    &__left {
        flex: 1 0 50%;
        max-width: 50%;
    }

    &__image {
        width: 100%;
        height: 100%;
        min-height: 515px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        border: 10px solid $color-white;
    }

    &__right {
        flex: 1 0 50%;
        max-width: 50%;
        padding: 0 40px;
    }

    &__close {
        position: absolute;
        right: -15px;
        top: -15px;
        width: 35px;
        height: 35px;
        font-size: 0;
        background-image: url("data:image/svg+xml,%3Csvg fill='%23ffffff' height='48' viewBox='0 0 24 24' width='48' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");        background-color: $color03;
        background-size: 25px;
        background-repeat: no-repeat;
        background-position: center center;
        color: $color-white;
        border-radius: 50%;
        cursor: pointer;

        &:hover {
            background-color: $color03-light;
        }

        BUTTON {
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        padding-left: 20px;

        H3 {
            font-size: $font-size-md;
        }

        P {
            margin-bottom: 0;
        }

        INPUT {
            margin-bottom: 10px;
        }

        INPUT[type="submit"] {
            background-color: $color03;

            &:hover {
                background-color: $color03-light;
            }
        }
    }

    @include below-desktop {

        &__inner {
            flex-direction: column;
            min-height: unset;
            justify-content: center;
        }

        &__left {
            display: none;
        }

        &__right {
            flex: 1 0 100%;
            max-width: 100%;
        }

        &__content {
            padding: 50px;

            LABEL {
                width: 100%;
            }

            INPUT {
                width: 100%;
                max-width: 100%;
            }

            INPUT[type="submit"] {
                width: unset;
            }
        }
    }

    @include below-mobile {
        &__content {
            padding: 50px 0;
        }
    }
}

.video-modal {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 40px;

    &__inner {
        box-shadow: $shadow02;  
    }
}