/******************************************************************
ALERT STYLES
Styles for alert and error components and messages
******************************************************************/


.alert-bar {
    display: none;
    margin-top: 20px;
    padding: 24px 30px;
    background: $color01-light;
    color: $color-white;
    animation: slideUpFadeIn 0.5s ease-out;

    &__text {
        margin: 0;
        color: $color-white;

        A {
            color: $color-white;

            &:hover {
                opacity: $opacity-md;
            }
        }
    }

    &--negative {
        background: $color-negative;
    }
    &--positive {
        background: $color-positive;
    }
    &--warning {
        background: $color-warning;
    }

    &.is-active {
        display: block;
    }
}

.alert-message {
    display: none;
    color: $color-neutral;

    &--positive {
        color: $color-positive;
    }

    &--negative {
        color: $color-negative;
    }

    &--subtle {
        color: $color-gray;
        font-style: italic;
    }

    &.is-active {
        display: block;
    }
}