.drawer {
    &__toggle {
        display: none;
    }

    @include below-mobile {
        position: fixed;
        left: 0; right: 0; top: 0; bottom: 0;
        width: 320px;
        transform: translateX(-100%);
        z-index: 8;
        transition: $transition03;

        &__overlay {
            display: none;
            content: "";
            position: fixed;
            left: 0; right: 0; top: 0; bottom: 0;
            width: 100%;
            height: 100%;
            background: $color01;
        }
    
        &__inner {
            position: relative;
            height: 100%;
        }

        &__content {
            overflow-y: auto;
            padding: 120px 40px 40px 40px;
            background: $color-white;
            height: 100%;
        }
    
        &__toggle {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            right: -48px;
            top: 120px;
            width: 48px;
            height: 48px;
            background: $color-white;
            color: $color-text;
            box-shadow: $shadow01;

            opacity: 0;
            visibility: hidden;
            transition: visibility $trans-time-sm linear, opacity $trans-time-sm $trans-type, color $trans-time-sm $trans-type;

            &.is-visible {
                opacity: 1;
                visibility: visible;
                transition: visibility $trans-time-sm linear, opacity $trans-time-sm $trans-type, color $trans-time-sm $trans-type;
            }

            &:hover {
                transition: $transition-default;
            }
        }

        &__close-icon {
            display: none;
            transform: rotate(90deg);
            margin-right: 4px;
        }

        &.is-active {
            transform: translateX(0);
            transition: $transition03;

            .drawer__inner {
                box-shadow: $shadow01;
            }

            .drawer__toggle {
                opacity: 1;
                visibility: visible;
                transition: visibility $trans-time-sm linear, opacity $trans-time-sm linear, color $trans-time-sm $trans-type;
            }

            .drawer__open-icon {
                display: none;
            }

            .drawer__close-icon {
                display: block;
            }
        }
    }


    &--page-nav {
        @include below-mobile {
            .drawer__content {
                padding: 100px 0 40px 0;
            }
        }
    }


    @include below-mobile-sm {
        &__content {
            padding: 120px 30px 40px 30px;
        }
    }

    @include below-mobile-xs {
        width: 272px;
    }
}