.social-share {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    &__title {
        margin: 0 40px 16px 0;
        font-weight: $font-weight-medium;
    }

    &__items {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -8px;
    }

    &__item {
        padding: 0 8px;
        margin-bottom: 16px;
    }

    &__anchor {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 36px;
        width: 36px;
        border-radius: 30px;
        transition: $transition-default;

        &:hover {
            transition: $transition-default;
        }
    }

    &__icon {
        width: 20px;
        height: 20px;
    }

    @include below-mobile {
        flex-direction: column;

        &__title {
            margin: 0;
        }

        &__anchor {
            height: 48px;
            width: 48px;
        }

        &__icon {
            width: 18px;
            height: 18px;
        }
    }
}