.hero {
    background-color: #e4e5e7;
    /*background-image: url('../../assets/images/algorigin-bottles-stone.png');
    background-repeat: no-repeat;
    background-position: center 70%;*/

    &__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        height: calc(100vh - 180px);
        min-height: 640px;
        max-height: 860px;
        padding: 70px 0 90px 0;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        max-width: 960px;
        text-align: center;
    }

    &__img {
        max-width: 100%;
        height: auto;
    }

    &__title {
        max-width: 740px;
    }

    .hero__actions {
        margin-top: -36px;
    }

    @include below-desktop {
        &__inner {
            height: calc(100vh - 80px);
        }
    }

    @include below-mobile {
        &__inner {
            min-height: 560px;
        }

        &__content {
            height: 100%;
            justify-content: space-evenly;
        }

        .hero__actions {
            margin-top: 20px;
        }
    }

    @include below-mobile-xs {
        &__inner {
            padding: 20px 0;
            min-height: 480px;
        }
    }
}


.story {
    padding: 160px 0;
    
    &__inner {
        display: flex;
        margin: 0 -40px;
    }

    &__main, &__aside {
        padding: 0 40px;
        flex: 1;
    }

    &__aside {
        display: flex;
        align-items: center;
    }

    &__title {
        margin: 0 0 20px 0;
    }

    &__title-mobile {
        display: none;
    }

    &__text {
        font-size: $font-size-lead;
    }

    &__play {
        position: relative;
    }

    &__player {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 360px;
        width: 100%;
        background-color: $color-gray-lightest;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
    }

    &__actions {
        margin-top: 60px;
    }

    @include below-desktop {
        &__inner {
            margin: 0 -20px;
        }

        &__main, &__aside {
            padding: 0 20px;
        }
    }

    @include below-mobile {
        padding: 80px 0 100px 0;

        &__inner {
            flex-direction: column-reverse;
        }

        &__aside {
            flex-direction: column;
            align-items: center;
            margin-bottom: 30px;
        }

        &__title {
            display: none;
        }

        &__title-mobile {
            display: block;
        }

        &__actions {
            text-align: center;
        }

        BLOCKQUOTE {
            margin: 0;
            padding: 0;
            text-align: center;

            &:before {
                display: none;
            }

            CITE {
                margin-top: 16px;
            }
        }
    }
}


.values {
    background: $color01;
    color: $color-white;
    padding: 80px 0 60px 0;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }

    &__title {
        padding-right: 120px;
        margin: 0 0 30px 0;
    }

    &__items {
        flex: 1;
        display: flex;
        justify-content: flex-end;
        margin: 0 -30px;
    }

    &__item {
        //flex: 1 0 25%;
        //max-width: 25%;
        padding: 0 30px;
        margin-bottom: 30px;
    }

    &__anchor {
        opacity: $opacity-md;
        transition: $transition-default;

        &:hover {
            opacity: 1;
            transition: $transition-default;
        }
    }

    @include below-desktop-lg {
        &__inner {
            display: block;
        }

        &__title {
            padding-right: 0;
            margin-bottom: 40px;
        }

        &__items {
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }

    @include below-mobile {
        &__title {
            text-align: center;
        }

        &__items {
            justify-content: center;
            margin: 0 -20px;
        }

        &__item {
            padding: 0 20px;
        }
    }
}


.shop-preview {
    padding: 140px 0;
    text-align: center;

    &__title {
        margin-bottom: 80px;
    }

    &__highlighted-items {
        position: relative;

        padding-bottom: 50px;
        border-bottom: 2px solid $color-border;
        margin-bottom: 20px;

        /*
        padding-bottom: 60px;
        border-bottom: 2px solid $color-border;
        margin-bottom: 60px;

        &:after {
            content: "";
            position: absolute;
            left: calc(50% - 12px);
            bottom: -12px;
            height: 24px;
            width: 24px;
            background: $color-white;
            border-left: 2px solid $color-border;
            border-top: 2px solid $color-border;
            transform: rotate(-135deg);
        }
        */
    }

    &__actions {
        margin-top: 35px;
    }

    &--mini {
        padding: 0 0 20px 0;

        .shop-preview__title {
            font-size: $font-size-h3;
