.newsletter {
    padding: 120px 0;
    background: $color02;

    &__inner {
        display: flex;
        align-items: center;
        margin: 0 -40px;
    }

    &__main, &__aside {
        padding: 0 40px;
        flex: 1;
    }

    &__aside {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }

    &__illustration {
        position: relative;
        height: 420px;
        width: 360px;
        margin-right: 68px;
    }

    &__title {
        margin: 0 0 20px 0;
    }

    &__title-mobile {
        display: none;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__form {
        margin: 40px 0 20px 0;
    }

    &__img {
        position: absolute;

        &:nth-child(1) {
            left: 0;
            top: 0;
            transform: rotate(-10deg);
            box-shadow: $shadow01;
            z-index: 2;
        }

        &:nth-child(2) {
            left: 116px;
            top: 10px;
            transform: rotate(16deg) scale(0.85);
            box-shadow: $shadow01;
            z-index: 1;
        }
    }

    @include below-desktop {
        &__inner {
            margin: 0 -20px;
        }

        &__main, &__aside {
            padding: 0 20px;
        }

        &__illustration {
            margin-right: 20px;
        }
        
        &__img {
            &:nth-child(1) {
                left: 0;
                top: 0;
                transform: rotate(-10deg) scale(0.75);
            }
    
            &:nth-child(2) {
                left: 116px;
                top: 10px;
                transform: rotate(16deg) scale(0.6);
            }
        }
    }

    @include below-mobile {
        padding: 80px 0;

        &__inner {
            flex-direction: column-reverse;
            margin-bottom: 20px;
        }

        &__title {
            display: none;
        }

        &__title-mobile {
            display: block;
            text-align: center;
        }

        &__aside {
            flex-direction: column;
            width: 100%;
        }

        &__main {
            text-align: center;

            input[type=text] {
                margin: 0 auto 16px auto;
            }
        }

        &__illustration {
            height: 280px;
            width: 260px;
            margin: 0 0 20px 0;
        }

        &__img {
            &:nth-child(1) {
                left: -60px;
                top: -70px;
                transform: rotate(-10deg) scale(0.6);
            }
    
            &:nth-child(2) {
                left: 40px;
                top: -70px;
                transform: rotate(16deg) scale(0.5);
            }
        }
    }
}


.instagram {
    &__inner {
        position: relative;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(5, minmax(140px, 1fr));
    }

    &__img {
        aspect-ratio: 1;
        object-fit: contain;
        width: 100%;
        height: auto;
    }

    &__label-wrap {
        position: absolute;
        left: 0; right: 0;
        top: 0; bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__label {
        position: relative;
        background: rgba($color-white, 0.9);
        backdrop-filter: blur(10px);
        min-width: 460px;
        min-height: 180px;
        padding: 40px 40px 50px 40px;
        text-align: center;
        text-decoration: none;

        &:hover {
            .instagram__icon {
                opacity: $opacity-md;
                transition: $transition-default;
            }
        }
    }

    &__icon {
        /*position: absolute;
        top: -25px;
        left: calc(50% - 35px);*/
        display: inline-block;
        height: 40px;
        width: 40px;
        margin-bottom: 4px;
        transition: $transition-default;
    }

    &__title {
        font-size: $font-size-md;
        margin: 0;
        text-transform: uppercase;
        letter-spacing: 2.5px;
    }

    &__subtitle {
        font-size: $font-size-lead;
        margin: 0;
    }

    @include below-desktop {
        &__inner {
            margin: 0 -40px;
        }

        &__items {
            grid-template-columns: repeat(4, minmax(120px, 1fr));
        }

        &__item {
            &:nth-child(9), &:nth-child(10) {
                display: none;
            }
        }
    }

    @include below-mobile {
        &__inner {
            margin: 0 -20px;
        }

        &__items {
            grid-template-columns: repeat(3, minmax(100px, 1fr));
        }

        &__item {
            &:nth-child(7), &:nth-child(8) {
                display: none;
            }
        }

        &__label {
            min-width: 360px;
        }
    }

    @include below-mobile-sm {
        &__items {
            grid-template-columns: repeat(2, minmax(100px, 1fr));
        }

        &__item {
            &:nth-child(5), &:nth-child(6) {
                display: none;
            }
        }

        &__label {
            padding: 36px 30px;
            min-width: 240px;
        }

        &__subtitle {
            font-size: $font-size-base;
        }

        &__icon {
            height: 32px;
            width: 32px;
        }
    }
}


