        border-bottom: 2px solid $color-border;
    }

    &__actions {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 -20px;
    }

    &__price, &__cart {
        padding: 0 20px;
    }

    &__discount-info {
        margin: 20px 0 0 0;
    }

    &__faq, &__related, &__appointment, &__info {
        margin: 120px 0;
    }

    &__appointment {
        padding-bottom: 30px;
    }

    &__bundled {
        margin-bottom: 40px;
    }

    &__bundles {
        margin-top: 40px;
    }

    @include below-desktop {
        &__main {
            margin: 0 -20px;
        }

        &__gallery, &__content {
            padding: 0 20px;
        }

        &__gallery {
