.contact {
    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 -20px;
    }

    &__item {
        flex: 1;
        padding: 0 20px;
        margin-bottom: 40px;
    }

    &__content-before {
        margin-bottom: 60px;
        text-align: center;
    }

    &__content-after {
        margin-top: 30px;
        text-align: center;
    }

    &__form {
        display: flex;
        flex-wrap: wrap;

        H3 {
            width: 100%;
            text-align: center;
        }

        .wpcf7 {
            width: 100%;

            FORM {
                display: flex;
                flex-wrap: wrap;
                padding: 30px 30px 60px 30px;
                background: $color02-light;

                P {
                    flex: 1;
                    padding: 0 20px;
                    margin-bottom: 0px;

                    INPUT {
                        margin-bottom: 0;
                    }

                    &:nth-child(5) {
                        flex: 1 0 100%;
                        max-width: 100%;
                        margin-top: 20px;

                        LABEL {
                            width: 100%;

                            TEXTAREA {
                                width: 100%;
                                max-width: 100%;
                            }
                        }
                    }

                    &:nth-child(6) {
                        text-align: center;
                        margin-top: 10px;

                        .wpcf7-response-output {
                            
                            border-color: transparent!important;
                        }

                        SPAN {
                            display: none;
                        }
                    }
                }

                &.failed {

                    .wpcf7-response-output {
                        width: 100%;
                        margin: 15px 0 0 0;
                        text-align: center;
                        border-color: transparent;
                        color: $color03;
                    }
                }
            }

            @include below-desktop-lg {

                FORM {
                    padding: 30px 15px 60px 15px;
                }

            }
            
            @include below-desktop { 
                FORM {
                    padding: 30px 30px 60px 30px;

                    P {
                        flex: 1 0 100%;
                        max-width: 100%;

                        LABEL {
                            width: 100%;

                            INPUT {
                                max-width: 100%;
                            }
                        }
                    }
                }
            }

            @include below-mobile-sm { 
                FORM {
                    padding: 30px 30px 60px 30px;

                    P {
                        padding: 0;
                    }
                }
            }
        }
    }
}


