        margin: 16px 0 0 0;
    }

    @include below-mobile {
        margin: 0;
        text-align: center;
        padding: 80px 0;

        &__inner {
            padding: 0;
        }

        &__cols {
            flex-direction: column;
        }

        &__image {
            height: 280px;
        }

        &__col {
            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }
    }

    @include below-mobile-sm {
        &__image {
            height: 240px;
        }
    }
}


