/* ESCAPE PRO */
.featured-course {
    position: relative;
    display: flex;

    &__outer {
        padding: 120px 40px 120px 40px;
        border-bottom: 2px solid $color02;
    }

    &__title {
        text-align: center;
        padding: 0 0 85px 0;
        margin: 0;
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        max-width: 960px;
        margin: auto;
    }

    &__left {
        flex: 1 0 50%;
        max-width: 50%;
    }

    &__right {
        flex: 1 0 50%;
        max-width: 50%;

        H3 {
            margin-bottom: 20px;
        }

        UL {
            list-style-type: disc;
            padding-left: 18px;
        }
    }

    @include below-desktop {
        text-align: center;

        &__left {
            flex: 1 0 100%;
            max-width: 100%;
            display: inline-flex;
            padding: 0 0 40px 0;
            
            IMG {
                margin: auto;
            }
        }

        &__right {
            flex: 1 0 100%;
            max-width: 100%;
            text-align: center;

            H3 {
                margin-bottom: 0.3em;
            }

            UL {
                list-style-position: inside;
                padding-left: 0;
            }

        }
    }

    @include below-mobile {
        &__outer {
            padding: 120px 20px 120px 20px;
        }

        &__left {

            IMG {
                width: 100%;
                max-width: 100%;
            }
        }
    }
}

.courses {
    position: relative;
    display: flex;
    padding: 100px 0 120px 0;

    &__title {
        text-align: center;
    }
}

.course-items {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -10px;
}

.course-item {
    display: flex;
    flex-wrap: wrap;
    flex: 1 0 25%;
    max-width: 25%;
    text-align: center;
    padding: 15px 10px;

    &__outer {
        display: flex;
        flex-wrap: wrap;
        height: 100%;
        border: 2px solid $color02;
    }

    &__image {
        width: 102%;
        height: auto;
        background-size: cover;
        background-repeat: no-repeat;
        margin: -2px;
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding: 20px 30px 30px 30px;
    }

    &__title {
        font-size: $font-size-base;
        font-weight: $font-weight-bold;
        margin-bottom: 0.6em;
    }

    &__description {
        font-size: $font-size-sm;
    }

    &__link {
        cursor: pointer;
        text-transform: uppercase;
        font-weight: $font-weight-bold;
    }

    @include below-desktop {
        flex: 1 0 33.33333%;
        max-width: 33.33333%;
    }

    @include below-mobile {
        flex: 1 0 50%;
        max-width: 50%;
    }

    @include below-mobile-sm {
        flex: 1 0 100%;
        max-width: 100%;
    }
} 

.doctors {
    position: relative;
    display: flex;
    position: relative;
    margin: 0 45px;
    padding: 120px 0 120px 0;
    background: $color02;

    &__inner {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: inherit;
    }

    &__left {
        flex: 1 0 70%;
        max-width: 70%;
        padding-right: 50px;

        &.editor-content {

            H2 {
                border-bottom: unset;
                padding-bottom: 0;
            }
        }

        UL {
            list-style-type: disc;
            padding: 0 0 18px 18px;
        }
    }

    &__right {
        flex: 1 0 30%;
        max-width: 30%;
        position: relative;
        background: $color01;
        color: $color02;
        padding: 50px;
        text-align: center;
        text-transform: uppercase;

        IMG {
            max-width: 120px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 50px;
        }
    }

    &__bottom {
        padding: 50px 0 0 0;

        A {
            color: $color-white;
            text-decoration: unset;
        }
    }

    @include below-desktop {

        &__left {
            flex: 1 0 100%;
            max-width: 100%;
            text-align: center;
            padding: 0 0 20px 0;

            UL {
                list-style-position: inside;
                padding-left: 0;
            }
        }

        &__right {
            flex: 1 0 100%;
            max-width: 300px;
            min-height: 400px;
            text-align: center;
        }

        &__bottom {
            display: inline-flex;
            flex: 1 0 100%;
            max-width: 100%;

            BUTTON {
                margin: auto;
            }
        }
    }

    @include below-mobile {
        margin: 0 20px;
    }

}

.vendors {
    position: relative;
    padding: 120px 0 120px 0;

    &__inner {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
    }

    &__title {
        flex: 1 0 100%;
        max-width: 100%;
        text-align: center;
    }

