    &__right {
        flex: 1 0 40%;
        max-width: 40%;
        padding-left: 40px;

        UL {
            list-style-type: disc;
            padding-left: 18px;
            
        }

        BUTTON {
            
            A {
                color: $color-white;
                text-decoration: unset;
            }
        }
    }

    @include below-desktop {
        &__left {
            flex: 1 0 100%;
            max-width: 100%;
        }

