.slider {
    overflow: hidden;

    &__items {
        display: flex;
    }

    &__item {
        flex: 1;
    }
}

.slider-nav {
    display: flex;
    justify-content: center;

    &__item {
        position: relative;
        display: inline-block;
        margin: 0 6px 6px 6px;
        height: 16px;
        width: 16px;
        background: $color-white;
        border-radius: 16px;
        opacity: $opacity-md;
        transition: $transition-default;

        &:after {
            content: "";
            position: absolute;
            top: 4px;
            left: 4px;
            width: 8px;
            height: 8px;
            background: $color01;
            border-radius: 4px;
            opacity: 0;
            transition: $transition-default;
        }

        &:hover {
            opacity: 1;
            transition: $transition-default;
        }

        &.is-active {
            opacity: 1;
            transform: scale(1.25);
            transition: $transition-default;
    
            &:after {
                opacity: 1;
                transition: $transition-default;
            }
        }
    }
}

.slider-controls {
    display: flex;

    &__arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        background: $color02;
        height: 60px;
        width: 60px;
        transition: $transition01;

        &:active {
            transform: scale(0.98);
            transition: $transition01;
        }
    }

    &__arrow--prev {
        @extend .has-animated-icon-left;
    }

    &__arrow--next {
        @extend .has-animated-icon-right;
    }

    &__icon {
        height: 28px;
        width: 28px;
    }

    @include below-mobile {
        &__arrow {
            height: 48px;
            width: 48px;
        }
    }
}