.tooltip {
    position: relative;
    display: inline-block;
    padding: 20px 30px;
    min-width: 320px;
    font-weight: $font-weight-medium;
    text-align: center;
    background: $color-white;
    box-shadow: $shadow02;
    color: $color-text;

    &:after {
        content: "";
        position: absolute;
        left: calc(50% - 8px);
        bottom: -8px;
        background: $color-white;
        width: 16px;
        height: 16px;
        transform: rotate(45deg);
    }

    @include below-mobile-xs {
        min-width: 240px;
        width: 100%;
    }
}