/******************************************************************
LIST STYLES
Styles for various layout lists that contain granular items
******************************************************************/


.icon-list {
    &__item {
        position: relative;
        padding-left: 40px;
    }

    &__icon {
        position: absolute;
        left: 0;
        top: 0;
    }
}

.logo-list {
    &__item {

    }
}

.blog-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px;

    &__item {
        padding: 0 16px;
        flex: 1 0 33.33333%;
        max-width: 33.33333%;
        margin-bottom: 60px;
    }

    @include below-desktop {
        &__item {
            flex: 1 0 50%;
            max-width: 50%;
        }
    }

    @include below-mobile {
        &__item {
            flex: 1 0 100%;
            max-width: 100%;

            &:last-child {
                margin-bottom: 20px;
            }
        }
    }
}

.label-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -4px;

    &__item {
        padding: 0 4px;
        margin-bottom: 8px;
    }

    @include below-mobile {
        justify-content: center;
    }
}

.socials-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -4px;

    &__item {
        padding: 0 4px;
        margin-bottom: 4px;
    }
}

.actions {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -4px;

    &__item {
        padding: 0 4px;
        margin-bottom: 4px;
    }

    &--gaps-md {
        margin: 0 -8px;

        .actions__item {
            padding: 0 8px;
        }
    }

    &--gaps-lg {
        margin: 0 -12px;

        .actions__item {
            padding: 0 12px;
        }
    }

    &--centered-vertically {
        align-items: center;
    }

    /*@include below-mobile-sm {
        &__item {
            width: 100%;
        }
    }*/
}

.product-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    &__item {
        flex: 1 0 20%;
        max-width: 20%;
        padding: 0 15px;
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
    }

    @include below-desktop-lg {
        .product-list__item {
            flex: 1 0 25%;
            max-width: 25%;
        }
    }

    @include below-desktop {
        .product-list__item {
            flex: 1 0 33.33333%;
            max-width: 33.33333%;
        }
    }

    @include below-mobile {
        .product-list__item {
            flex: 1 0 50%;
            max-width: 50%;
        }
    }

    @include below-mobile-sm {
        .product-list__item {
            flex: 1 0 100%;
            max-width: 100%;
        }
    }

    &--shop {
        .product-list__item {
            flex: 1 0 25%;
            max-width: 25%;
        }

        @include below-desktop-lg {
            .product-list__item {
                flex: 1 0 33.33333%;
                max-width: 33.33333%;
            }
        }

        @include below-desktop {
            .product-list__item {
                flex: 1 0 50%;
                max-width: 50%;
            }
        }

        @include below-mobile {
            .product-list__item {
                flex: 1 0 33.33333%;
                max-width: 33.33333%;
            }
        }

        @media (max-width: 639px) {
            .product-list__item {
                flex: 1 0 50%;
                max-width: 50%;
            }
        }

        @include below-mobile-sm {
            .product-list__item {
                flex: 1 0 100%;
                max-width: 100%;
            }
        }
    }

    &--centered {
        justify-content: center;
    }
}


.category-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -20px;

    &__item {
        padding: 0 20px;
        margin-bottom: 24px;
    }
}


.icon-items-list {
    &__item {
        position: relative;
        padding: 0 0 40px 90px;
        padding-bottom: 30px;  

        &:not(:last-child) {
            border-bottom: 2px solid $color-border;
            margin-bottom: 30px;
        }
    }

    &__icon-wrap {
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 64px;
        width: 64px;
        color: $color-white;
        background: $color01; //$color02-light;
        border-radius: 32px;
    }

    &__icon {
        margin-top: 2px;
        height: 32px;
        width: 32px;
    }

    &__title {
        font-size: $font-size-base;
        font-weight: $font-weight-medium;
    }
    
    &__text {
        margin: 16px 0 0 0;
    }

    @include below-mobile {
        &__icon-wrap {
            display: inline-flex;
            position: relative;
            margin-bottom: 16px;
        }

        &__item {
            padding-left: 0;
            text-align: center;
        }
    }
}