/******************************************************************
MISC SMALL COMPONENTS
Various tiny helper components that don't belong to any category
******************************************************************/


.separator {
    display: block;
    margin: 30px 0;
    height: 2px;
    width: 100%;
    background: $color-border;
    font-size: 0;

    &--md {
        margin: 60px 0;
    }

    &--lg {
        margin: 80px 0;
    }
}

.scroll-anchor {
    position: absolute;
    top: -100px; // Set this to the desired height offset
    visibility: hidden;
    opacity: 0;

    &--center {
        top: 40vh;
    }

    @include below-desktop-xl {
        //top: -15px;
    }
    @include below-mobile {
        //top: -10px;
    }
}

.video-player {
    display: block;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
}