/******************************************************************
COMPATIBILITY
Exceptions and fixes for browser compatibility issues
Modernizr classes: no-svg, no-webp, no-cssgrid, no-csscalc, no-cssgradients
******************************************************************/


MAIN {
    // IE fix
    display: block;
    position: relative;
}


// IE SVG Image size fix
.ie9 img[src$=".svg"] {
    width: 100%;
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    img[src$=".svg"] {
        width: 100%;
    }
}