/******************************************************************
GDPR
Styles for GDPR plugin
******************************************************************/

#cookie-notice {
    position: fixed;
    min-width: 100%;
    height: auto;
    visibility: hidden;
    z-index: 80;

    @extend .container;
    background: transparent !important;
    display: flex;
    justify-content: flex-end;
    bottom: 30px;
    left: 0; right: 0;
    pointer-events: none;

    .cookie-notice-container {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        text-align: center;
        width: 100%;
        min-width: 480px;
        background: $color-white;
        pointer-events: initial;
        max-width: 480px;
        padding: 24px 20px;
        color: $color-text;
        font-size: 14px;
        box-shadow: $shadow01;
        //border: 1px solid $color-gray;
    }

    #cn-notice-text {
        margin: 0 0 8px 0;
    }

    .cookie-notice-buttons {
        margin: 0
    }

    .cn-button {
        margin: 0 8px;
    }

    @include below-mobile {
        bottom: 20px;

        .cookie-notice-container {
            min-width: 100%;
        }

        .cn-button {
            margin-bottom: 8px;
        }
    }

    @include below-mobile-sm {
        #cn-notice-text {
            margin-bottom: 20px;
        }
    }
}