/******************************************************************
NOTIFICATION STYLES
Styles for notification components
******************************************************************/

.notification-popup {
    position: fixed;
    width: 100%;
    z-index: 12;
    opacity: 0;
    transform: translateY(-16px);
    visibility: hidden;
    transition: visibility $trans-time linear, opacity $trans-time $bezier01, transform $trans-time $bezier01, top $trans-time-lg $bezier01;
    transition: $transition-default;
    //pointer-events: none;

    &__inner {
        position: relative;
        display: block;
        margin-left: auto;
        min-width: 180px;
        max-width: 360px;
        padding: 24px 40px 28px 40px;
        font-weight: $font-weight-medium;
        background: $color-white;
        box-shadow: $shadow02;
        overflow: hidden;
        transition: $transition-default;
    }

    &__text {
        margin: 0;
    }

    &__icon {
        position: absolute;
        top: calc(50% - 18px);
        left: 40px;
        width: 28px;
        height: 28px;
    }

    &--is-link {
        .notification-popup__inner {
            text-decoration: none;

            &:hover {
                background: $color02-light;
                transition: $transition-default;
            }
        }
    }

    &--positive {
        .notification-popup__inner {
            color: $color-white;
            background: $color01;
        }

        &.notification-popup--is-link {
            &:hover {
                .notification-popup__inner {
                    background: rgba($color01, 0.75);
                }
            }
        }
    }

    &--negative {
        .notification-popup__inner {
            color: $color-white;
            background: $color-negative;
        }

        &.notification-popup--is-link {
            &:hover {
                .notification-popup__inner {
                    background: rgba($color-negative, 0.75);
                }
            }
        }
    }

    &--icon-left {
        .notification-popup__inner {
            padding-left: 100px;
        }

        .notification-popup__icon {
            left: 40px;
        }
    }

    &--icon-right {
        .notification-popup__inner {
            padding-right: 100px;
        }

        .notification-popup__icons {
            left: initial;
            right: 40px;
        }
    }

    &.is-active {
        opacity: 1;
        transform: translateY(0);
        visibility: visible;
        transition: visibility $trans-time linear, opacity $trans-time $bezier01, transform $trans-time $bezier01, top $trans-time-lg $bezier01;
    }

    @include below-mobile-sm {
        &__inner {
            max-width: 480px;
        }
    }
}

.notification-focus-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at right top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.6) 30%);
    pointer-events: none;
    z-index: 11;
    opacity: 0;
    visibility: hidden;
    transition: visibility $trans-time linear, opacity $trans-time $bezier01, transform $trans-time $bezier01;
    transition: $transition-default;
    pointer-events: none;

    &.is-active {
        opacity: 1;
        visibility: visible;
        transition: visibility $trans-time linear, opacity $trans-time $bezier01, transform $trans-time $bezier01;
        transition: $transition-default;
    }
}