/******************************************************************
TABS STYLES
Styling for nav tabs
******************************************************************/


.nav-tabs {
    display: flex;
    justify-content: center;

    &__item {
        flex: 1;

        &:not(:last-child) {
            .nav-tabs__anchor {
                border-right: none;
            }
        }
    }

    &__anchor {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 13px 24px;
        min-height: 60px;
        text-align: center;
        font-weight: $font-weight-medium;
        line-height: 1;
        white-space: nowrap;
        background: $color02-light;
        border: 2px solid $color-border02;
        transition: $transition-default;

        &:after {
            content: "";
            position: absolute;
            left: calc(50% - 10px);
            bottom: -10px;
            width: 20px;
            height: 20px;
            background: rgba($color01, 0);
            transform: rotate(45deg);
            transition: $transition-default;
            z-index: -1;
        }

        &:hover {
            color: $color-text;
            background: $color02-lighter; // Equivilent to rgba($color02-light, $opacity-md);
        }

        &:hover, &.is-active {
            opacity: 1;
            transition: $transition-default;
        }

        &.is-active {
            border-color: $color01;
            background-color: $color01;
            color: $color-white;

            &:after {
                background: rgba($color01, 1);
                transition: $transition-default;
            }
        }
    }

    &__icon {
        margin: -2px 10px 0 0;
    }

    &__mobile-title {
        display: none;
    }

    @include below-mobile {
        &__mobile-title {
            display: block;
        }
    }
}

.nav-tabs-content {
    position: relative;
}

.tab-content {
    position: absolute;
    left: 0; top: 0; right: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(8px);
    transition: visibility $trans-time-sm linear, opacity $trans-time-sm $trans-type, transform $trans-time-md $trans-type;

    &.is-active {
        position: relative;
        height: auto;
        opacity: 1;
        visibility: visible;
        pointer-events: inherit;
        transform: translateX(0);
        transition: visibility $trans-time-md linear, opacity $trans-time-md $trans-type, transform $trans-time-md $trans-type;
    }

    &--dissolve-mobile {
        @include below-mobile {
            position: relative;
            height: auto;
            opacity: 1;
            visibility: visible;
            pointer-events: inherit;
            transform: translateX(0);
            transition: visibility $trans-time-md linear, opacity $trans-time-md $trans-type, transform $trans-time-md $trans-type;
        }
    }
}