/******************************************************************
BREADCRUMB STYLES
Styles for breadcrumb components
******************************************************************/


.breadcrumbs {
    &__item {
        display: inline-block;

        &:after {
            content: "/";
            padding: 0 10px;
        }

        &:last-child {
            &:after {
                display: none;
            }
        }
    }

    &--text-sm {
        font-size: 1.6rem;
    }
}