/******************************************************************
EDITOR BLOCKS
Styles for WordPress Block Editor (Gutenberg) default and custom blocks
******************************************************************/

// Add theme colors
$theme-colors: (
	'brand-light' 		    : $color01,
	'brand-dark' 	        : $color02,
    'brand-accent'          : $color03,
	'brand-gray-light'   	: $color-gray-light,
    'brand-gray' 		    : $color-gray,
    'brand-gray-dark'       : $color-gray-dark,
);
@each $name, $color in $theme-colors {
	.has-#{$name}-color {
		color: $color;
	}

	.has-#{$name}-background-color {
		background-color: $color;
	}
}

// Override editor buttons with theme buttons
.wp-block-button__link {
    @extend .button;
    text-decoration: none !important;
    color: $color-white !important;
}

// Override editor separator with theme separator
.wp-block-separator {
    @extend .separator;
}

.wp-block-buttons {
    margin-bottom: 24px;
}

.wp-block-image, .wp-block-embed, .wp-block-table {
    FIGCAPTION {
        margin-top: 12px;
        font-size: $font-size-sm;
        color: $color-gray-light;
        text-align: center;
    }
}

.wp-block-image {
    margin: 40px 0;

    IMG {
        margin: 0;
    }

    FIGURE {
        line-height: 0;
    }

    FIGCAPTION {
        line-height: $line-height-base;
    }

    .aligncenter {
        text-align: center;

        FIGCAPTION {
            text-align: center; 
        }

        IMG {
            display: inline-block;
        }
    }

    .alignleft {
        text-align: left;

        FIGCAPTION {
            text-align: left; 
        }

        IMG {
            display: inline-block;
        }
    }

    .alignright {
        text-align: right;

        FIGCAPTION {
            text-align: right; 
        }

        IMG {
            display: inline-block;
        }
    }

    &.is-style-rounded {
        IMG {
            border-radius: 9999px;
        }
    }
}

.wp-block-columns {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px;

    @include below-mobile-sm {
        flex-direction: column;
    }
}

.wp-block-column {
    flex: 1;
    padding: 0 20px;
    margin-bottom: 24px;

    > *:first-child {
        margin-top: 0;
    }

    > *:last-child {
        margin-bottom: 0;
    }

    &.is-vertically-aligned-center {
        align-self: center;
    }

    &.is-vertically-aligned-bottom {
        align-self: flex-end;
    }
}

.wp-block-image figcaption {
    text-align: center;
    font-size: $font-size-sm;
}

.wp-block-embed {
    &.is-type-video {
        .wp-block-embed__wrapper {
            position: relative;
    
            &:before {
                content: "";
                display: block;
                padding-top: 56.25%;
            }
        }

        IFRAME {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%;
            width: 100%;
        }
    }

    &.is-type-wp-embed {
        BLOCKQUOTE.wp-embedded-content {
            margin: 0 0 40px 0;
            padding: 36px;
            background: $color-gray-light;
            
            &:before {
                display: none;
            }
        }
    }
}

.wp-block-table {
    margin: 0 0 24px 0;
}