.video-player {
    display: block;
    width: 100%;
    height: 100%;
    min-width: 100%;
    max-width: 100%;

    &--keep-aspect {
        position: relative;

        &:before {
            content: "";
            display: block;
            padding-top: 56.25%;
        }

        IFRAME {
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            height: 100%;
            width: 100%;
        }
    }
}