/******************************************************************
ITEM STYLES
Styles for granular items (mainly part of lists)
******************************************************************/

.blog-card {
    margin: 0 auto;
    max-width: 432px;

    &__image {
        margin-bottom: 36px;
        line-height: 0;
        overflow: hidden;
        
        .blog-card__anchor {
            display: inline-block;
            background: $color-white;
            
        }
    }

    &__anchor {
        display: block;
        height: 280px;
        position: relative;

        &:hover {
            transform: translateY(0);

            .blog-card__img {
                opacity: $opacity-md;
                transform: scale(1.1);
                transition: all $trans-time-lg $trans-type;
            }
        }
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all $trans-time-lg $trans-type;
    }

    &__categories {
        margin-bottom: 20px;
    }

    &__title {
        margin: 0 0 16px 0;
    }

    &__actions {
        margin-top: 36px;
    }

    @include below-mobile {
        text-align: center;

        &__image {
            margin: 0 auto 30px auto;
        }

        &__actions {
            margin-top: 30px;
            justify-content: center;
        }
    }
}

.testimonial-card {
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    text-align: center;

    &__inner {
        padding: 300px 40px 60px 40px;
        background: linear-gradient(180deg, rgba(38,57,56,0) 20%, rgba(38,57,56,1) 60%);
    }

    &__title {
        margin: 0 0 20px 0;
        line-height: 1;
    }

    &__subtitle {
        opacity: $opacity-md;
    }

    &__text {
        position: relative;
        margin: 30px 0 0 0;
        padding: 0 42px;
        font-size: $font-size-md;

        &:before, &:after {
            content: "";
            position: absolute;
            width: 34px;
            height: 28px;
            background: url('../../assets/icons/icon-quote-white.svg');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: 50% 50%;
            opacity: 0.2;
            z-index: 0;
        }

        &:before {
            top: 0;
            left: 0;
        }

        &:after {
            right: 0;
            top: 0;
            transform: rotate(180deg);
        }
    }

    &__actions {
        margin: 16px 0 0 0;
    }

    &__more {
        font-size: 1.6rem;
        color: rgba($color-white, 1);

        &:hover {
            color: rgba($color-white, $opacity-md);
        }
    }

    &__socials {
        margin: 42px 0 0 0;
        justify-content: center;
    }

    @include below-desktop {
        &__inner {
            padding: 320px 0 40px 0;
        }

        &__text {
            font-size: $font-size-base;
        }
    }
}

.product-card {
    position: relative;
    padding-bottom: 86px;
    max-width: 320px;
    height: 100%;
    width: 100%;
    border: 2px solid $color-border;
    transition: $transition-default;

    &__inner {
        //border-top: 2px solid $color-border;
        padding: 20px 20px 0 20px;
        text-align: left;
    }

    &__title {
        font-size: 1.8rem;
        margin: 0 0 12px 0;
    }

    &__anchor {
        text-decoration: none;
    }

    &__description {
        font-size: $font-size-xs;
    }

    &__bottom {
        position: absolute;
        bottom: 22px;
        left: 0; right: 0;
        width: 100%;
        padding: 0 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__image {
        position: relative;
        display: block;
        background: $color-gray-lightest;
        line-height: 1;
        overflow: hidden;
    }

    &__image-anchor {
        position: relative;
        display: block;
        height: 240px;
        width: 100%;

        &:hover {
            .product-card__img {
                transition: $transition-default;
    
                &:nth-child(2) {
                    opacity: 1;
                    transition: $transition-default;
                }
            }
        }
    }

    &__img {
        position: absolute;
        left: 0; right: 0; top: 0; bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 1;
        transition: $transition-default;

        &:nth-child(2) {
            opacity: 0;
        }
    }

    &:hover {
        box-shadow: $shadow02;
        transition: $transition-default;
    }

    &.is-active {
        .product-card__img {
            opacity: $opacity-md;
            transition: $transition-default;

            &:nth-child(2) {
                opacity: 1;
            }
        }
    }

    @include below-mobile-sm {
        width: 100%;
    }
}

.expanded-product-card {
    &__inner {
        display: flex;
        text-align: left;
        align-items: center;
    }

    &__image {
        position: relative;
        flex: 3;
        height: 420px;
        margin-right: 64px;
        background: $color-gray-lightest;
        overflow: hidden;
    }

    &__img {
        /*position: absolute;
        top: 50%;
        left: 50%;
        width: auto;
        height: auto;
        max-height: none;
        max-width: none;
        min-height: 100%;
        min-width: 100%;
        transform: translate(-50%, -50%);*/
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        flex: 2;
        padding: 40px 0;
    }
    
    &__anchor {
        text-decoration: none;
    }

    &__title {
        margin: 0 0 30px 0;
    }

    &__description {
        font-size: $font-size-md;
        margin: 0 0 36px 0;
    }

    &__price {
        margin: 0 0 36px 0;
    }

    &__label {
        position: relative;
        padding-left: 36px;

        .icon {
            position: absolute;
            left: 0;
            top: 4px;
            height: 18px;
            width: 18px;
        }
    }

    @include below-desktop {
        &__image, &__content {
            flex: 1;
        }
    }

    @include below-mobile {
        &__title {
            font-size: $font-size-md;
            margin-bottom: 20px;
        }

        &__label {
            font-size: $font-size-base;

            .icon {
                top: 1px;
            }
        }

        &__description {
            margin-bottom: 16px;
        }

        &__price {
            margin-bottom: 24px;
        }
    }

    @media screen and (max-width: 600px) {
        &__inner {
            flex-direction: column;
        }

        &__image {
            margin-right: 0;
        }

        &__content {
            text-align: center;
            padding-bottom: 20px;
        }
    }

    &--sm {
        .expanded-product-card__image, .expanded-product-card__content {
            flex: 1;
        }

        .expanded-product-card__image {
            height: 400px;
            margin-right: 40px;
        }

        .expanded-product-card__title, .price__standard, .price__new, .price__old {
            font-size: 2.8rem;
        }

        .expanded-product-card__description {
            font-size: $font-size-base;
            margin-bottom: 24px;
        }

        @media screen and (max-width: 600px) {
            .expanded-product-card__image {
                margin-right: 0;
            }
        }
    
    }
}

.faq-item {
    margin-bottom: 16px;

    &__toggle {
        position: relative;
        padding: 16px 60px 16px 30px;
        border: 2px solid $color02;
        background: $color02-light; //$color-white;
        min-height: 60px;
        font-weight: $font-weight-medium;
        cursor: pointer;

        &:after {
            content: "";
            position: absolute;
            right: 30px;
            top: 20px;
            height: 16px;
            width: 16px;
            background: url('../../assets/icons/icon-close.svg');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: 50% 50%;
            transform: rotate(45deg);
            transition: $transition-default;
        }

        &.is-active {
            &:after {
                transform: rotate(0);
                transition: $transition-default;
            }
        }
    }

    &__content {
        padding: 0 40px;
        border: 2px solid $color02;
        border-top: none;
        background: $color-white; //$color02-light;

        &.is-active {
            padding-top: 36px;
            padding-bottom: 36px;
        }
    }

    @include below-mobile {
        &__content {
            &.is-active {
                padding-top: 24px;
                padding-bottom: 24px;
            }
        }
    }
}


.contact-item {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 40px;
    background: $color02-light;
    min-width: 240px;

    &__icon {
        height: 48px;
        width: 48px;
        margin-bottom: 16px;
    }

    &__link {
        font-size: 2.0rem;
        font-weight: $font-weight-medium;
        text-decoration: none;
    }

    &__text {
        margin: 8px 0 0 0;
    }

}


.search-result-card {
    &__inner {
        position: relative;
        padding-right: 60px;
    }

    &__title {
        margin: 0 0 4px 0;
        font-size: $font-size-md;
        
    }

    &__anchor {
        text-decoration: none;
    }

    &__type {
        margin: 0;
        font-size: $font-size-base;
        font-weight: $font-weight-normal;
        color: $color-gray-light;
    }

    &__excerpt {
        margin: 8px 0 0 0;
    }

    &__actions {
        position: absolute;
        right: 0;
        top: calc(50% - 24px);
    }
}


.ambassador-card {
    position: relative;

    &__heading {
        margin-bottom: 30px;
    }

    &__title {
        margin: 0 0 8px 0;
    }

    &__inner {
        display: flex;
        margin: 0 -30px;
    }

    &__photo {
        flex: 1;
        padding: 0 30px;
    }
    
    &__content {
        flex: 3;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 30px 40px 30px;
    }

    &__img {
        max-width: 480px;
        height: auto;
    }

    &__photo-credit {
        margin: 16px 0 0 0;
        color: $color-gray-light;
    }

    &__quote {
        font-size: $font-size-md;
        font-weight: $font-weight-medium;
    }

    &__actions {
        margin-top: 36px;
        border-top: 2px solid $color-border;
        padding-top: 30px;
    }

    &__socials {
        margin-top: 8px;
    }

    @include below-desktop-lg {
        &__img {
            max-width: 360px;
        }
    }

    @include below-desktop {
        &__inner {
            margin: 0 -20px;
        }

        &__photo, &__content {
            padding: 0 20px;
        }

        &__img {
            max-width: 320px;
        }
    }

    @include below-mobile {
        &__heading {
            text-align: center;
        }

        &__inner {
            text-align: center;
            flex-direction: column;
            align-items: center;
        }

        &__photo {
            margin-bottom: 30px;
        }

        .actions {
            justify-content: center;
        }
    }

    @include below-mobile-xs {
        &__img {
            max-width: 260px;
        }

        &__quote {
            margin: 0;
        }
    }
}