/******************************************************************
HEADER STYLES
Styles specific to the site header
******************************************************************/


.header-offset {
    background: $color-white;
    height: 180px;

    @include below-desktop {
        height: 80px;
    }
}
.home .header-offset {
    background: #e4e5e7;
}
.archive .header-offset, .page-template-template-algae .header-offset, .page-template-template-blog .header-offset, .page-template-template-contact .header-offset, .search .header-offset, .category .header-offset, .page-template-template-experience .header-offset, .page-template-template-pro .header-offset {
    background: $color01;
}
.single-post .header-offset {
    background: $color02;
}


.header {
    position: fixed;
    left: 0; right: 0;
    top: 0;
    box-shadow: none;
    z-index: 10;
    transition: $transition03;

    &__top {
        position: relative;
        border-bottom: 2px solid $color-gray-lightest;
        z-index: 13;
    }

    &__main {
        position: relative;
        display: flex; 
        align-items: center;
        background: $color-white;
        //padding: 24px 0;
        min-height: 120px;
        transition: $transition03;
        z-index: 12;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__content {
        display: flex;
    }

    &__logo {
        margin-right: 45px;
    }

    &__menu {
        display: flex;
        align-items: center;
    }

    &__actions {
        margin-left: 60px;
        animation: fadeIn 1s ease-out forwards;
    }

    .header__secondary-actions {
        display: none;

        A, BUTTON {
            display: block;
            line-height: 0;
        }
    }

    &__button {
        display: block;
    }

    &__search-button {
        padding: 0;
        margin-top: 2px;
    }

    &__account-button {
        margin-top: 2px;
    }

    &__mobile-toggle {
        display: none;
        margin-right: -5px;
        align-items: center;
    }

    &__searchbar {
        border-top: 2px solid $color-gray-lightest;
        background: $color-white;
        padding: 10px 0;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        margin-top: -82px;
        transition: margin-top $trans-time $bezier01, opacity $trans-time $bezier01, visibility $trans-time linear;
        z-index: 11;

        &.is-active {
            opacity: 1;
            visibility: visible;
            margin-top: 0;
            pointer-events: initial;
            transition: margin-top $trans-time $bezier01, opacity $trans-time $bezier01, visibility $trans-time linear;
        }
    }

    &__cart-notification {
        top: 220px;
        right: 0;
        left: 0;
    }

    &__secondary-region-toggle {
        display: none;
    }

    &__secondary-search-button {
        display: block;
    }

    &__region-button {
        padding: 0;
        margin-top: 3px;
    }

    @include below-desktop-xl {
        &__logo {
            margin-right: 30px;
        }

        &__button {
            min-width: 0;
        }
    }

    @include below-desktop-lg {
        &__espace {
            display: none;
        }
    }

    @include below-desktop {
        box-shadow: $shadow02;

        &__main {
            min-height: 80px;
            padding: 10px 0;
        }

        .header__actions {
            display: none;
        }

        &__mobile-toggle {
            display: block;
        }

        &__cart-notification {
            top: 120px;
        }

        .header__secondary-actions {
            display: flex;
            margin: 0 8px 0 auto;
        }

        &__secondary-region-toggle {
            display: block;
        }

        &__secondary-search-button {
            display: none;
        }

        &__menu {
            position: fixed;
            top: 80px; left: 0; right: 0; bottom: 0;
            //padding-top: 80px;
            background: $color-white;
            width: 100%;
            height: 100%;
            align-items: flex-start;
            overflow-y: auto;

            visibility: hidden;
            opacity: 0;
            pointer-events: none;
            transition: opacity $trans-time $bezier01, visibility $trans-time linear;
        }

        &__searchbar {
            position: fixed;
            margin-top: 0;
            bottom: 0;
            left: 0; right: 0;
            border-bottom: 2px solid $color-border;
            z-index: 15;
            transition: $transition-default;
        }
    }

    @include below-mobile-sm {
        .header__secondary-actions {
            margin-right: 0;

            .actions__item {
                padding: 0 4px;

                .icon {
                    height: 20px;
                    width: 20px;
                    margin-top: 3px;
                }
            }
        }
    }

    @include below-mobile-xs {
        .header__secondary-account-button {
            display: none;
        }
    }
}


.top-bar {
    padding: 15px 0;
    background: $color-white;
    min-height: 60px;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__main {
        display: flex;
        align-items: center;
    }

    &__login, &__call, &__region, &__cart {
        text-decoration: none;
        margin-left: 60px;
    }

    &__region {
        position: relative;
    }

    &__region-dropdown {
        position: absolute;
        top: 42px;
        left: 0;
        background: $color-white;
        box-shadow: $shadow01;
        padding: 36px 36px 40px 36px;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transform: translateY(-8px);
        transition: opacity $trans-time $bezier01, transform $trans-time $bezier01, visibility $trans-time linear;
        z-index: 15;

        &.is-active {
            opacity: 1;
            visibility: visible;
            pointer-events: inherit;
            transform: translateY(0);
            transition: opacity $trans-time $bezier01, transform $trans-time $bezier01, visibility $trans-time linear;
        }
    }

    @include below-desktop-xl {
        &__login, &__call, &__region, &__cart {
            margin-left: 40px;
        }
    }

    @include below-desktop {
        //display: none;
        margin-top: -60px;
        z-index: 9;

        &__call, &__login, &__cart, &__messages, .region-toggle__button, .region-toggle__flag {
            display: none;
        }

        &__region-dropdown {
            position: fixed;
            left: 0; right: 0; top: 0; bottom: 0;
            padding: 100px 20px 40px 20px;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: rgba($color-white, 0.98);
            z-index: 9;
        }
    }
}


.header-logo {
    display: flex;
    align-items: center;
    z-index: 101;
  
    &__anchor {
        display: block;
        height: 48px;
        opacity: 1;
        transition: $transition03;
  
        &:hover {
            opacity: $opacity-md;
            transition: $transition-default;
        }
    }
  
    &__img {
        display: block;
        height: 100%;
        max-width: initial;
    }

    @include below-desktop-xl {
        &__anchor {
            height: 42px;
        }
    }

    @include below-desktop {
        &__anchor {
            height: 36px;
        }
    }

    @include below-mobile-sm {
        &__anchor {
            height: 32px;
        }
    }

    @include below-mobile-xs {
        &__anchor {
            height: 28px;
        }
    }
}


.header-nav {
    .menu {
        display: flex;
        //flex-wrap: wrap;

        &__item {
            position: relative;

            &.is-top-level {
                margin: 0 25px;

                > .menu__anchor {
                    display: flex;
                    align-items: center;
                    padding: 0 5px;
                    height: 120px;
                    transition: all $trans-time $bezier01;
                }

                &.menu__item--has-children {
                    overflow: hidden;

                    &:after {
                        content: "";
                        position: absolute;
                        left: calc(50% - 10px);
                        bottom: -10px;
                        width: 20px;
                        height: 20px;
                        background: $color-gray-lightest;
                        transform: rotate(45deg);
                        opacity: 0;
                        transition: all $trans-time-sm $bezier01;
                    }

                    > .menu__anchor {
                        position: relative;
                        padding-right: 30px;
    
                        &:after {
                            content: "";
                            position: absolute;
                            top: calc(50% - 7px);
                            right: 5px;
                            background-image: url('../../assets/icons/icon-caron-down.svg');
                            background-repeat: no-repeat;
                            background-size: contain;
                            height: 14px;
                            width: 14px;
                            opacity: 1;
                            transition: $transition-default;
                        }

                        &:hover {
                            &:after {
                                opacity: $opacity-md;
                                transition: $transition-default;
                            }
                        }
                    }

                    &:hover {
                        &:after {
                            opacity: 1;
                            transition: all $trans-time-sm $bezier01;
                        }

                        > .menu__mega {
                            opacity: 1;
                            visibility: visible;
                            pointer-events: initial;
                            transition: top $trans-time-sm $bezier01, opacity $trans-time-sm $bezier01, visibility $trans-time-sm $bezier01;
    
                            .menu__sub-menu {
                                transform: translateY(0);
                                transition: $transition-default;
                            }
                        }
                    }
                }
            }
        }

        &__anchor {
            text-decoration: none;
            font-weight: $font-weight-medium;
        }

        // Megamenu styles
        &__mega {
            position: fixed;
            display: block;
            width: 100%;
            top: 180px;
            left: 0; right: 0;
            background: $color-gray-lightest;
            padding: 60px 0;
            min-height: 440px;
            box-shadow: $shadow03;
            z-index: 14;

            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            transition: top $bezier01 $trans-time-sm, opacity $trans-time-sm $bezier01, visibility $trans-time-sm $bezier01;

            .menu__sub-menu {
                display: flex;
                flex-wrap: wrap;
                margin: 0 -40px;

                transform: translateY(-4px);
                transition: $transition-default;
            }

            .menu__item {
                &.is-first-level {
                    flex: 1 0 50%;
                    max-width: 50%;
                    padding: 0 40px;

                    > .menu__sub-menu {
                        margin: 0 -20px;
                    }
                }

                &.is-second-level {
                    flex: 1 0 50%;
                    max-width: 50%;
                    padding: 0 20px;
                    margin-bottom: 12px;
                }
            }

            .menu__anchor {
                &.is-first-level {
                    display: block;
                    width: 100%;
                    padding-bottom: 12px;
                    border-bottom: 2px solid rgba($color-gray-light, 0.5);
                    pointer-events: none;
                    margin-bottom: 30px;
                }

                &.is-second-level {
                    font-weight: $font-weight-normal;
                }
            }
        }
    }

    @include below-desktop-xl {
        .menu {
            &__item {
                &.is-top-level {
                    margin: 0 15px;
                }
            }
        }
    }

    @include below-desktop-lg {
        .menu {
            &__item {
                &.is-top-level {
                    margin: 0 12px;

                    &.menu__item--has-children {
                        > .menu__anchor {
                            padding-right: 25px;
                        }
                    }
                }
            }
        }
    }

    @include below-desktop {
        width: 100%;

        .menu {
            flex-direction: column;

            &__item {
                &.is-top-level {
                    margin: 0;
                    border-bottom: 2px solid $color-border;

                    &:after {
                        display: none;
                    }

                    &:first-child {
                        border-top: 2px solid $color-border;
                    }

                    > .menu__anchor {
                        height: 60px;
                        padding: 0 20px;
                    }

                    &.menu__item--has-children {
                        > .menu__anchor { 
                            padding-right: 40px;

                            &:after {
                                right: 25px;
                            }
                        }
                    }
                }
            }

            &__mega {
                position: relative;
                top: 0;
                padding: 20px 0;
                min-height: 0;
    
                opacity: 0;
                visibility: hidden;
                pointer-events: none;
                height: 0;
                padding: 0;
                transition: height $trans-time $bezier01, padding $trans-time $bezier01, opacity $trans-time $bezier01, visibility $trans-time linear;
    
                &.is-active {
                    display: flex;
                    height: auto;
                    padding: 20px 0;
                    visibility: visible;
                    opacity: 1;
                    pointer-events: inherit;
                    transition: height $trans-time $bezier01, padding $trans-time $bezier01, opacity $trans-time $bezier01, visibility $trans-time linear;
                }

                .menu__sub-menu {
                    margin: 0 -10px;
                    transform: translateY(0);
                }

                .menu__item {
                    &.is-first-level {
                        padding: 0 10px;

                        >.menu__sub-menu {
                            margin: 0 -10px;
                        }
                    }

                    &.is-second-level {
                        flex: 1 0 100%;
                        max-width: 100%;
                        padding: 0 10px;
                        margin-bottom: 8px;
                    }
                }

                .menu__anchor {
                    margin-bottom: 0;

                    &.is-first-level {
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }

    @include below-mobile-xs {
        .menu {
            > &__anchor {
                font-size: $font-size-base;
            }
        }
    }
}


.mega-menu {
    &__inner {
        position: relative;
        padding-right: 400px;
    }

    &__hover-img {
        position: absolute;
        top: 0; right: 0;
        opacity: 0;
        width: 320px;
        height: 320px;
        background: $color-gray-light;
        transition: all $trans-time-sm ease-out;

        &.is-active {
            opacity: 1;
            transition: all $trans-time-sm ease-out;
        }
    }

    @include below-desktop {
        &__inner {
            padding-right: 0;
        }

        &__hover-img {
            display: none;
        }
    }
}


.mobile-menu-toggle {
    position: relative;
    height: 32px;
    width: 38px;
    padding: 5px;
    cursor: pointer;
    z-index: 100;

    SPAN {
        position: absolute;
        height: 2px;
        border-radius: 10px;
        font-size: 0;
        background: $color-text;
        transition: all $trans-time $bezier01;
    }

    &__inner {
        position: relative;
        height: 100%;
        width: 100%;
    }

    &__open, &__close {
        position: absolute;
        top: 0; right: 0; bottom: 0; left: 0;
        height: 100%;
        width: 100%;
        overflow: hidden;
    }

    &__open {
        padding: 0 3px;

        SPAN {
            width: 80%;

            &:nth-child(1) {
                top: 0; left: 20%;
                transition-delay: 0.5s;
            }
            &:nth-child(2) {
                top: calc(50% - 1px); left: 10%;
                transition-delay: 0.625s;
            }
            &:nth-child(3) {
                bottom: 0; left: 0px;
                transition-delay: 0.75s;
            }
        }
    }

    &__close {
        height: 28px;
        width: 28px;
        padding: 0;
        transform: rotate(-45deg) translateY(-2px);

        SPAN {
            &:nth-child(1) {
                left: calc(50% - 1px); top: 0;
                height: 0%;
                width: 2px;
                transition-delay: 0s;
            }
            &:nth-child(2) {
                left: 0; top: calc(50% - 1px);
                width: 0%;
                height: 2px;
                transition-delay: 0.125s;
            }
        }
    }

    @include below-mobile-sm {
        height: 28px;
        width: 34px;

        &__close {
            height: 24px;
            width: 24px;
            transform: rotate(-45deg) translateY(-3px);
        }
    }
}


.is-scrolled {
    @include above-desktop {
        .header {
            margin-top: -60px;
            box-shadow: $shadow02;
            transition: $transition03;
    
            &__main {
                //padding: 12px 0;
                min-height: 82px;
                transition: $transition03;
            }
    
            &__actions {
                display: none;
            }
    
            &__secondary-actions {
                display: flex;
                animation: fadeIn 1s ease-out forwards;
            }

            &__cart-notification {
                top: 120px;
            }
        }

        .header-nav {
            .menu {
                &__mega {
                    top: 81px;
                    transition: $transition03;
                }

                &__item {
                    &.is-top-level {    
                        > .menu__anchor {
                            height: 80px;
                            transition: $transition-default;
                        }
                    }
                }
            }
        }
    
        .header-logo {
            &__anchor {
                height: 42px;
                transition: $transition03;
            }
        }
    }
}


.is-search-open {
    @include above-desktop {
        .header {
            box-shadow: $shadow02;
        }
    }
}


.is-mobile-menu-open {
    @extend .has-no-scroll-y;

    .header {
        &__menu {
            visibility: visible;
            opacity: 1;
            pointer-events: inherit;
            transition: opacity $trans-time $bezier01, visibility $trans-time linear;
        }

        &__searchbar {
            opacity: 1;
            visibility: visible;
            pointer-events: inherit;
            position: fixed;
            transition: opacity $trans-time $bezier01, visibility $trans-time linear;
        }
    }

    .mobile-menu-toggle {
        &__open {
            SPAN {
                width: 0%;

                &:nth-child(1) {
                    transition-delay: 0s;
                }
                &:nth-child(2) {
                    transition-delay: 0.125s;
                }
                &:nth-child(3) {
                    transition-delay: 0.25s;
                }
            }
        }

        &__close {
            SPAN {
                &:nth-child(1) {
                    height: 100%;
                    width: 2px;
                    transition-delay: 0.5s;
                }

                &:nth-child(2) {
                    width: 100%;
                    height: 2px;
                    transition-delay: 0.625s;
                }
            }
        }
    }
}