/******************************************************************
WOOCOMMERCE
Styles to override woocommerce defaults
******************************************************************/


/* ACCOUNT */
A.added_to_cart.wc-forward {
    display: none;
}

.woocommerce-password-strength {
    margin-bottom: 4px;

    &.short, &.bad {
        color: $color-negative;
    }
    &.good {
        color: $color-neutral;
        margin-bottom: 24px; // This rating has no hint, so move margin here
    }
    &.strong {
        color: $color-positive;
        margin-bottom: 24px; // This rating has no hint, so move margin here
    }
}
.woocommerce-password-hint {
    display: block;
    line-height: 1.4;
    font-size: 12px;
    margin-bottom: 24px;
}

.woocommerce-privacy-policy-text {
    font-size: $font-size-sm;
}

.address-field {
    .woocommerce-input-wrapper {
        display: block;
    }
}

@include below-desktop {
	.woocommerce table.woocommerce-orders-table thead {
		display: none;
	}
	.woocommerce table.woocommerce-orders-table tr {
		display: block;
		margin-bottom: 30px;
	}
	.woocommerce table.woocommerce-orders-table td {
		display: block;
		text-align: right !important;
		&:not(:last-child) {
			border-bottom: 0;
		}
	}
	.woocommerce table.woocommerce-orders-table td::before {
		content: attr(data-title) ": ";
		font-weight: 700;
		float: left;
	}
}

.woocommerce-orders-table__cell.woocommerce-orders-table__cell-order-actions A {
    &:not(:last-child) {
        margin-right: 8px;
    }
}


/* CART */
.cart-table {
    &__product-inner {
        display: flex;
        align-items: center;
		min-width: 240px;
    }

	&__thumbnail {
        margin-right: 20px;
        transition: $transition-default;

        &:hover {
            opacity: $opacity-md;
            transition: $transition-default;
        }

		IMG {
			max-width: 100px;
			max-height: 100px;
            width: 100px;
            height: 100px;
			margin: 0;
		}
	}

	&__quantity {
		INPUT {
			margin: 0;
			max-width: 100px;
            min-width: 100px;
		}
	}

	&__mobile-title {
		display: none;
		font-weight: $font-weight-medium;
		line-height: $line-height-base;
	}

	TBODY {
		TD {
			padding: 24px 20px;
		}
	}

	@include below-desktop {
		&__name {
			min-width: 160px;
		}
	}

	@include below-mobile {
        &__product {
            display: block;
            border-bottom: 0;
        }

        &__product-inner {
            display: block;
        }

		&__mobile-title {
			display: block;
		}

		&__item {
			text-align: center;
		}

		&__heading {
			display: none;
		}

		&__thumbnail {
			display: none;
		}

		&__quantity {
			.quantity INPUT {
				margin: 8px 0 0 0;
			}
		}

		&__name, &__price, &__quantity, &__subtotal {
			border-bottom: 0;
			padding-bottom: 0;
		}

		&__name, &__price, &__quantity, &__subtotal, &__remove {
			display: block; 
		}
	}
}

.product-remove {
	&__button {
		color: $color-negative;
	}

	&__desktop-text {
		display: inline-block;
        font-size: 28px;
	}
	&__mobile-text {
		display: none;
	}

	@include below-mobile {
		&__desktop-text {
			display: none;
		}
		&__mobile-text {
			display: inline-block;
		}
	}
}

.coupon {
    margin: 40px 0;
    display: block;
    width: 100%;
    padding: 32px 40px;
    background: $color02-light;
    border: 2px solid $color02;

    &__inner {
        max-width: 640px;
        margin: 0 auto;
    }

    &__form {
        display: flex;
        margin: 0 -8px;
        flex-wrap: wrap;
    }

    .coupon__cta {
        margin: 0 0 8px 0;
        font-size: $font-size-sm;
    }

    &__field {
        flex: 1;
        padding: 0 8px;
		margin-bottom: 8px;
        
        .text-field__input {
            margin: 0;
        }
    }

    &__button {
        padding: 0 8px;

        .button {
            min-height: 60px;
        }
    }

    @include below-mobile {
        text-align: center;

        .coupon__cta {
            margin-bottom: 16px;
        }
    }

	@include below-mobile-sm {
		&__button {
			width: 100%;
		}
	}
}

.wc-proceed-to-checkout {
    margin: 24px 0 0 0;
}

.cart-discounts {
    margin: 32px 0 0 0;
}

.cart-collaterals {
    margin: 40px 0 0 0;

    &__actions {
        justify-content: flex-end;
    }

    @include below-mobile {
        &__actions {
            justify-content: center;
        }
    }

    @include below-mobile-sm {
        &__actions {
            .actions__item {
                width: 100%;
            }
        }
    }
}

.cart {
    &--empty {
        text-align: center;
    }

    &__empty-text {
        color: $color-gray;
    }
}

.woocommerce-result-count, .orderby {
    margin: 0;
}

.shipping-calculator-form {
    margin-top: 20px;
}


/* CHECKOUT */
.woocommerce-checkout-review-order-table {
    margin-bottom: 24px;

    .product-name {
        min-width: 200px;
    }
}

.wc_payment_methods {
	list-style-type: none !important;
	margin: 48px 0 !important;
	padding: 36px 0 !important;
	border-top: 2px solid $color-border !important;
	border-bottom: 2px solid $color-border !important;

	li.wc_payment_method {
        display: flex;
        position: relative;
        flex-wrap: wrap;
        align-items: center;
        padding: 10px 20px;
        margin-bottom: 8px;
        border: 2px solid $color02;

		P {
			margin: 0 !important;
            padding: 10px 0;
		}

        LABEL {
            display: flex;
            flex-direction: row;
            margin-bottom: 0;
            padding: 10px 0;
            font-weight: $font-weight-bold;

            IMG {
                position: absolute;
                right: 20px;
                max-height: 30px;

                &:nth-child(2) {
                    right: 75px;
                }

                &:nth-child(3) {
                    right: 130px;
                }

                &:nth-child(4) {
                    right: 185px;
                }

            }

            A {
                margin-left: 15px;
            }
            


        }

        .payment_box {
            border-top: 1px solid $color02;
            flex: 1 0 100%;
            max-width: 100%;
        }

        INPUT {
            margin-right: 8px;
        }

		&:before {
			display: none;
		}
	}
}

.woocommerce-shipping-methods {
    LI {
        margin-bottom: 16px;
        vertical-align: middle;
    }

    LABEL {
        margin: 0;
    }

    INPUT {
        margin-right: 8px;
    }
}

.woocommerce-shipping-destination {
    margin: 0 0 8px 0;
}

#ship-to-different-address {
    margin-bottom: 60px;

    LABEL {
        font-size: $font-size-base;
    }

    SPAN {
        font-weight: $font-weight-medium;
    }
}

.required {
    color: $color03;
}


/* ERROR MESSAGES */
.woocommerce-notices-wrapper {
    margin-bottom: 30px;
}
.page-template-template-cart {

    .woocommerce-notices-wrapper {
        margin: 30px auto;
    }

    .woocommerce-form-login-toggle {
        margin: 0 auto;
        max-width: 1200px;
        padding: 20px 30px;
        border: 2px solid $color02;
        background-color: $color02-light;
        text-align: center;
    }

    .woocommerce-form-login {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 0 auto;
        max-width: 1200px;
        padding: 30px 30px 20px 30px;
        background-color: $color02-light;
        border-left: 2px solid $color02;
        border-right: 2px solid $color02;
        border-bottom: 2px solid $color02;

        P {
            flex: 1 0 100%;
            max-width: 100%;
            text-align: center;
        }

        .form-row {
            flex: 1 0 50%;
            max-width: 50%;
            padding: 0 10px;

            LABEL {
                padding: 0 20px;
            }

            .input-text {
                max-width: 100%;
            }
        }
    }

    @include below-desktop-lg {
        .woocommerce-form-login-toggle {
            margin: 0 40px;
        }

        .woocommerce-form-login {
            margin: 0 40px;
        }

    }

    @include below-mobile {
        .woocommerce-form-login-toggle {
            margin: 0 20px;
            padding: 20px;
        }
        .woocommerce-form-login {
            margin: 0 20px;
        }
        .woocommerce-form-login {

            .form-row {
                flex: 1 0 100%;
                max-width: 100%;

                .input-text {
                    min-width: unset;
                    margin-bottom: 0;
                }
            }

        }
    }
}
.woocommerce-error {
	list-style-type: none;
	background: $color-negative;
	color: $color-white;
	padding: 20px 30px !important;

	LI {
		padding-left: 0 !important;

		&:before {
			display: none !important;
		}
	}

    A {
        color: $color-white;
        text-decoration: underline;
    }
}

.woocommerce-message {
	background: $color02-light;
	color: $color-text;
	padding: 20px 30px !important;
	margin-bottom: 24px;

	A {
		color: $color-text;
        text-decoration: underline;
        transition: $transition-default;

        &:hover {
            opacity: $opacity-md;
            transition: $transition-default;
        }
	}
}


/* WPC Product Bundles  */
.woosb-bundles {
    margin-top: 40px;

    .woosb_before_text {
        font-weight: $font-weight-medium;
        margin-bottom: 12px;
    }

    .woosb_after_text {
        font-weight: $font-weight-medium;
        margin-top: 16px;
    }

    .woosb-products {
        border: unset;
    
        .woosb-product {
            display: flex;
            padding: 20px 30px;
            background: $color02-light;
            border: 2px solid $color-border02;
    
            .woosb-thumb {
                width: 100px;
    
                IMG {
                    width: 80px;
                    height: 80px;
                    max-width: unset;
                }
            }
    
            .woosb-title {
                font-weight: $font-weight-medium;
                font-size: 2.2rem;
                
                A {
                    text-decoration: none;
                }
            }
    
            .woosb-price {
                text-align: right;
                width: unset;
                padding: 0 20px 0 0;
    
                DEL {
                    color: $color-gray;
                    opacity: 1;
                    font-weight: $font-weight-medium;
                    
                    SPAN {
                        line-height: 1;
    
                        BDI {
                            // Override plugin !important settings 
                            font-size: $font-size-base!important;
                        }
                    }
                }
    
                .amount {
                    font-size: $font-size-md!important;
                    font-weight: $font-weight-medium;
                    line-height: 1;
                }
            }
        }
    }

    @include below-mobile {
        .woosb-products {
            .woosb-product {
                padding: 20px;

                .woosb-thumb {
                    display: none;
                }

                .woosb-title {
                    line-height: 1.2;
                }

                .woosb-price {
                    padding-right: 8px;
                }
            }
        }
    }
}

.woosb-bundled {
    .woosb_before_text {
        font-weight: $font-weight-medium;
        margin-bottom: 12px;
    }

    .woosb_after_text {
        font-weight: $font-weight-medium;
        margin-top: 16px;
    }

    .woosb-products {
        border: none;

        .woosb-product {
            padding: 20px 30px;
            border: 2px solid $color-border02;

            &:not(:last-child) {
                border-bottom: none;
            }

            .woosb-thumb {
                width: 100px;
    
                IMG {
                    width: 80px;
                    max-width: unset;
                    height: 80px;
                }
            }

            .woosb-title {
                A {
                    text-decoration: none;
                }
            }
    
            .woosb-price {
                text-align: right;
                width: unset;
                padding: 0 20px 0 0;
    
                DEL {
                    color: $color-gray;
                    opacity: 1;
                    font-weight: $font-weight-medium;
                    
                    SPAN {
                        line-height: 1;
    
                        BDI {
                            // Override plugin !important settings 
                            font-size: $font-size-base!important;
                        }
                    }
                }
    
                .amount {
                    font-size: $font-size-md!important;
                    font-weight: $font-weight-medium;
                    line-height: 1;
                }
            }
        }
    }
}


/* Disable country fields */
#calc_shipping_country_field, #shipping_country_field .select2, #billing_country_field .select2, #billing_country, #shipping_country {
    position: relative;
    opacity: $opacity-md;
    pointer-events: none;
    cursor: not-allowed;

    &:after {
        content: "";
        position: absolute;
        left: 0; right: 0; top: 0; bottom: 0;
        width: 100%; height: 110%;
        cursor: not-allowed;
    }
}

#billing_country_field, #shipping_country_field {
    pointer-events: none;
    cursor: not-allowed;
}

#select2-billing_country-results, #select2-shipping_country-results, [aria-owns="select2-billing_country-results"], [aria-owns="select2-shipping_country-results"] {
    display: none;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    font-size: 0;
}


/* MISC */
.form-row {
	position: relative; 
	
	.woocommerce-form__input-checkbox {
		@extend .checkbox__input;
	}
	.woocommerce-form__label-for-checkbox {
		@extend .checkbox__label;
	}
}