/******************************************************************
ANIMATIONS
Various animations to be used throughout the project
******************************************************************/

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.anim-fadeout {
    animation: fadeOut $trans-time-lg $trans-type;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.anim-fadein {
    animation: fadeIn $trans-time-lg $trans-type;
}

@keyframes loader {
	0%,
	80%,
	100% {
        transform: scale(1);
	}
	40% {
        transform: scale(1.36);
	}
}
.anim-loader {
    animation: loader $trans-time-lg $trans-type infinite;
}

@keyframes buttonIconBounceLeft {
    0% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
    25% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9);
    }
    26% {
        opacity: 0;
        transform: translateX(10px) scale(0.9);
    }
    55% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
}

@keyframes buttonIconBounceRight {
    0% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
    25% {
        opacity: 0;
        transform: translateX(10px) scale(0.9);
    }
    26% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9);
    }
    55% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
}

@keyframes slideDownFadeIn {
    0% {
        transform: translateY(-8px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes addCartOverlay {
    0% {
        opacity: 0;
    }

    20% {
        opacity: 1;
    }

    80% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes notificationPopup {
    0% {
        opacity: 0;
        transform: translateY(-8px);
    }
    20% {
        opacity: 1;
        transform: translateY(0);
    }
    80% {
        opacity: 1;
        transform: translateY(0);
    }
    100%  {
        opacity: 0;
        transform: translateY(-8px);
    }
}