/******************************************************************
GRID
Custom grid system
******************************************************************/


.site-wrapper {
	max-width: $max-container-width;
	margin: 0 auto;
}

.container {
	max-width: $container-width;
	width: 100%;
	padding: 0 $unit;
	margin: 0 auto;

	&--sm {
		max-width: 960px;
	}

	&--md {
		max-width: 1280px;
	}

	&--lg {
		max-width: 1660px;
	}

	&--xl {
		max-width: 1920px;
	}

	@include below-mobile {
		padding: 0 $unit-mobile;
	}
}

.grid {
	$self: &;

	display: flex;
	flex-flow: row wrap;
	margin-left: -20px;
	margin-right: -20px;

	&--reverse {
		flex-direction: row-reverse;
	}
	&--centered {
		justify-content: center;
	}
	&--spaced {
		justify-content: space-between;
	}
	&--column {
		flex-direction: column;
	}

	&--gutters-none {
		margin-left: 0;
		margin-right: 0;

		#{$self}__col {
			padding-left: 0;
			padding-right: 0;
		}
	}
	&--gutters-xs {
		margin-left: -5px;
		margin-right: -5px;

		> #{$self}__col {
			padding-left: 5px;
			padding-right: 5px;
		}
	}
	&--gutters-sm {
		margin-left: -10px;
		margin-right: -10px;

		> #{$self}__col {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	&--gutters-lg {
		margin-left: -30px;
		margin-right: -30px;

		> #{$self}__col {
			padding-left: 30px;
			padding-right: 30px;
		}

		@include below-mobile {
			margin-left: -20px;
			margin-right: -20px;
	
			> #{$self}__col {
				padding-left: 20px;
				padding-right: 20px;
			}
		}
	}
	&--gutters-xl {
		margin-left: -40px;
		margin-right: -40px;

		> #{$self}__col {
			padding-left: 40px;
			padding-right: 40px;
		}

		@include below-mobile {
			margin-left: -20px;
			margin-right: -20px;
	
			> #{$self}__col {
				padding-left: 20px;
				padding-right: 20px;
			}
		}
	}

	&__col {
		flex: 1;
		padding-left: 20px;
		padding-right: 20px;

		&#{$self}__col--2 {
			flex: 1 0 50%;
			max-width: 50%;
		}

		&#{$self}__col--3 {
			flex: 1 0 33.33333%;
			max-width: 33.33333%;
		}

		&#{$self}__col--4 {
			flex: 1 0 25%;
			max-width: 25%;
		}

		&#{$self}__col--5 {
			flex: 1 0 20%;
			max-width: 20%;
		}

		&#{$self}__col--6 {
			flex: 1 0 16.66666%;
			max-width: 16.66666%;
		}

		&#{$self}__col--7 {
			flex: 1 0 14.28571%;
			max-width: 14.28571%;
		}

		&#{$self}__col--8 {
			flex: 1 0 12.5%;
			max-width: 12.5%;
		}

		&#{$self}__col--2of3 {
			flex: 1 0 66.66666%;
			max-width: 66.66666%;
		}

		&#{$self}__col--3of4 {
			flex: 1 0 75%;
			max-width: 75%;
		}

		// LARGE DESKTOP BREAKPOINT
		@include below-desktop-xl {
			&#{$self}__col--lg {
				flex: 1 0 100%;
				max-width: 100%;
			}

			&#{$self}__col--lg2 {
				flex: 1 0 50%;
				max-width: 50%;
			}

			&#{$self}__col--lg3 {
				flex: 1 0 33.33333%;
				max-width: 33.33333%;
			}

			&#{$self}__col--lg4 {
				flex: 1 0 25%;
				max-width: 25%;
			}

			&#{$self}__col--lg5 {
				flex: 1 0 20%;
				max-width: 20%;
			}

			&#{$self}__col--lg6 {
				flex: 1 0 16.66666%;
				max-width: 16.66666%;
			}

			&#{$self}__col--lg7 {
				flex: 1 0 14.28571%;
				max-width: 14.28571%;
			}

			&#{$self}__col--lg8 {
				flex: 1 0 12.5%;
				max-width: 12.5%;
			}

			&#{$self}__col--lg2of3 {
				flex: 1 0 66.66666%;
				max-width: 66.66666%;
			}

			&#{$self}__col--lg3of4 {
				flex: 1 0 75%;
				max-width: 75%;
			}
		}

		// DESKTOP BREAKPOINT
		@include below-desktop {
			&#{$self}__col--md {
				flex: 1 0 100%;
				max-width: 100%;
			}

			&#{$self}__col--md2 {
				flex: 1 0 50%;
				max-width: 50%;
			}

			&#{$self}__col--md3 {
				flex: 1 0 33.33333%;
				max-width: 33.33333%;
			}

			&#{$self}__col--md4 {
				flex: 1 0 25%;
				max-width: 25%;
			}

			&#{$self}__col--md5 {
				flex: 1 0 20%;
				max-width: 20%;
			}

			&#{$self}__col--md6 {
				flex: 1 0 16.66666%;
				max-width: 16.66666%;
			}

			&#{$self}__col--md7 {
				flex: 1 0 14.28571%;
				max-width: 14.28571%;
			}

			&#{$self}__col--md8 {
				flex: 1 0 12.5%;
				max-width: 12.5%;
			}

			&#{$self}__col--md2of3 {
				flex: 1 0 66.66666%;
				max-width: 66.66666%;
			}

			&#{$self}__col--md3of4 {
				flex: 1 0 75%;
				max-width: 75%;
			}
		}

		// MOBILE BREAKPOINT
		@include below-mobile {
			&#{$self}__col--sm {
				flex: 1 0 100%;
				max-width: 100%;
			}

			&#{$self}__col--sm2 {
				flex: 1 0 50%;
				max-width: 50%;
			}

			&#{$self}__col--sm3 {
				flex: 1 0 33.33333%;
				max-width: 33.33333%;
			}

			&#{$self}__col--sm4 {
				flex: 1 0 25%;
				max-width: 25%;
			}

			&#{$self}__col--sm5 {
				flex: 1 0 20%;
				max-width: 20%;
			}

			&#{$self}__col--sm6 {
				flex: 1 0 16.66666%;
				max-width: 16.66666%;
			}

			&#{$self}__col--sm7 {
				flex: 1 0 14.28571%;
				max-width: 14.28571%;
			}

			&#{$self}__col--sm8 {
				flex: 1 0 12.5%;
				max-width: 12.5%;
			}

			&#{$self}__col--sm2of3 {
				flex: 1 0 66.66666%;
				max-width: 66.66666%;
			}

			&#{$self}__col--sm3of4 {
				flex: 1 0 75%;
				max-width: 75%;
			}

			&#{$self}__col--sm-fullwidth {
				padding-left: 0;
				padding-right: 0;
			}
		}

		// MOBILE BREAKPOINT (SMALL)
		@include below-mobile-sm {
			&#{$self}__col--xs {
				flex: 1 0 100%;
				max-width: 100%;
			}

			&#{$self}__col--xs2 {
				flex: 1 0 50%;
				max-width: 50%;
			}

			&#{$self}__col--xs3 {
				flex: 1 0 33.33333%;
				max-width: 33.33333%;
			}

			&#{$self}__col--xs4 {
				flex: 1 0 25%;
				max-width: 25%;
			}

			&#{$self}__col--xs5 {
				flex: 1 0 20%;
				max-width: 20%;
			}

			&#{$self}__col--xs6 {
				flex: 1 0 16.66666%;
				max-width: 16.66666%;
			}

			&#{$self}__col--xs7 {
				flex: 1 0 14.28571%;
				max-width: 14.28571%;
			}

			&#{$self}__col--xs8 {
				flex: 1 0 12.5%;
				max-width: 12.5%;
			}

			&#{$self}__col--xs2of3 {
				flex: 1 0 66.66666%;
				max-width: 66.66666%;
			}

			&#{$self}__col--xs3of4 {
				flex: 1 0 75%;
				max-width: 75%;
			}
		}
	}

}