.select2 {
    max-width: 480px;
    margin-bottom: 16px;

    .select2-selection {
        display: block;
        height: 60px;
        width: 100%;
        min-width: 240px;
        padding: 19px 20px;
        font-weight: $font-weight-normal;
        line-height: 1;
        color: $color-text;
        vertical-align: middle;
        box-shadow: none;
        border: 2px solid $color-border02;
        border-radius: 0;
        background-color: $color-white;
        box-shadow: none;
        position: relative;
        padding-right: 50px;
        background-image: url('../../assets/icons/icon-caron-down.svg');
        background-repeat: no-repeat;
        background-size: 14px;
        background-position: right 20px center;
        transition: $transition-default;
    
        &:focus,
        &:active {
            border: 2px solid $color01;
            outline: none;
            transition: $transition-default;
        }

        .select2-selection__rendered {
            line-height: 1;
            padding: 0;
        }

        .select2-selection__arrow {
            display: none;
        }
    }
}

.select2-container {
    .select2-dropdown {
        border: 2px solid $color-border02;
        border-top: none;
        border-radius: none;
    }

    .select2-search {
        padding: 4px 20px;
    }

    .select2-search__field {
        height: 48px;
        padding: 8px;
        max-width: 100%;
        border: $color-border;
    }

    .select2-results__option {
        padding: 8px 20px;
    }

    .select2-results__option--highlighted[aria-selected], .select2-results__option--highlighted[data-selected] {
        background-color: $color01;
    }
}