/******************************************************************
MODULE STYLES
Reusable/modular content blocks that make up the site layout
******************************************************************/

.section {
    padding: 160px 0;
}


.section-heading {
    margin-bottom: 60px;

    &__title {
        margin: 0;
    }

    // MODIFIERS
    &--has-options {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .section-heading__options {
            margin-left: 60px;
        }
    }

    @include below-desktop {
        text-align: center;
    }

    @include below-mobile {
        margin-bottom: 80px;

        &--has-options {
            display: block;

            .section-heading__title {
                margin-bottom: 6px;
            }

            .section-heading__options {
                margin-left: 0;
            }
        }
    }

    @include below-mobile-sm {
        margin-bottom: 60px;
    }
}


.page-content {
    padding: 60px 0;

    &__socials {
        margin-top: 80px;
        //border-top: 2px solid $color-border;
        //padding-top: 20px;
        padding: 16px 30px 4px 30px;
        background: $color02-light;
    }

    &--has-sidebar {
        .page-content__sidebar {
            margin-bottom: 72px;
        }

        .page-content__inner {
            display: flex;
            margin: 0 -40px;
        }

        .page-content__main {
            flex: 3;
            padding: 34px 40px 0 40px;
            min-width: 0;
        }

        .page-content__aside {
            flex: 1;
            padding: 0 40px;
            min-width: 320px;
        }
    }

    &--sidebar-right {
        .page-content__inner {
            flex-direction: row-reverse;
        }

        .page-content__main {
            padding-top: 0;
        }
    }

    &--sidebar-sticky {
        .page-content__aside {
            position: relative;
        }

        .page-content__sidebar {
            position: sticky;
            top: 120px;
            left: 0; right: 0;
        }
    }

    @include below-desktop-lg {
        &--has-sidebar {
            .page-content__inner {
                margin: 0 -25px;
            }

            .page-content__main {
                padding-left: 25px;
                padding-right: 25px;
            }

            .page-content__aside {
                padding-left: 25px;
                padding-right: 25px;
            }
        }
    }

    @include below-desktop {
        &--has-sidebar {
            .page-content__inner {
                margin: 0 -20px;
            }

            .page-content__main {
                padding-left: 20px;
                padding-right: 20px;
            }

            .page-content__aside {
                padding-left: 20px;
                padding-right: 20px;
                min-width: 280px;
            }
        }
    }

    @include below-mobile {
        &__socials {
            padding: 24px 30px 4px 30px;
        }

        &__title {
            text-align: center;
        }

        &--has-sidebar {
            .page-content__aside {
                display: none;
            }
        }

        &--mobile-sidebar-keep {
            .page-content__inner {
                flex-direction: column-reverse;
            }
            .page-content__aside {
                margin-top: 80px;
                display: block;
            }
        }
    }

}


.page-heading {
    padding: 74px 0 86px 0;
    color: $color-white;
    background-color: $color01;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    text-align: center;

    &__title {
        margin: 0 0 8px 0;
    }

    &__lead {
        margin: 0;
    }

    &__breadcrumb {
        margin: 16px 0 0 0;
    }

    &--light {
        color: $color-text;
        background-color: $color02;
    }

    &--dark {
        color: $color-white;
        background-color: $color01;

        A {
            color: rgba($color-white, 1);

            &:hover {
                color: rgba($color-white, $opacity-md);
            }
        }
    }

    &--neutral {
        color: $color-text;
        background-color: $color-gray-lightest;
    }
}


.logo {
    display: flex;
    align-items: center;
    height: 72px;
    max-width: 100%;

    &__img {
        max-height: 100%;
        width: auto;
        fill: $color-white;
        filter: brightness(0) invert(1);
    }
}


.label {
    display: inline-block;
    padding: 11px 24px;
    min-height: 36px;
    background: $color02;
    font-size: $font-size-sm;
    font-weight: $font-weight-bold;
    line-height: 1;
    text-align: center;
}


.social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 42px;
    width: 42px;
    border-radius: 24px;
    background: $color02;
    text-decoration: none;
    opacity: $opacity-md;
    transition: $transition-default;

    &__icon {
        color: $color01;
        height: 20px;
        width: 20px;
    }

    &:hover {
        opacity: 1;
        transition: $transition-default;
    }
}


.message-slider {
    &__item {
        height: 0;
        opacity: 0;
        visibility: hidden;
        transition: visibility 0s linear, opacity 0s linear;
    
        &.is-active {
            height: auto;
            opacity: 1;
            visibility: visible;
            transition: visibility $trans-time-lg linear, opacity $trans-time-lg linear;
        }
    }
}


.product-slider {
    position: relative;

    &__items {
        padding: 30px 0 35px 0;
    }

    &__arrows {
        position: absolute;
        left: 0; right: 0;
        top: 200px;
        justify-content: space-between;
        margin: 0 -30px;
        pointer-events: none;
    }

    &__arrow {
        pointer-events: initial;
    }

    @include below-mobile {
        &__arrows {
            margin: 0 -20px;
        }
    }

    @include below-mobile-sm {
        &__item {
            display: flex;
            justify-content: center;
        }
    }
}


.price {
    font-size: 2.0rem;
    font-weight: $font-weight-medium;

    &__main {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        line-height: $line-height-sm;
    }

    &__old {
        color: $color-gray-light;
        font-weight: $font-weight-normal;
        font-size: 1.8rem;
    }

    &__new {
        margin-right: 8px;
    }

    &--product-card {
        .price__new {
            margin-right: 5px;
        }
    }

    &--product-card-expanded, &--product {
        font-size: $font-size-lg;

        .price__old {
            font-size: 3.2rem;
        }

        @media (max-width: 600px) {
            .price__main {
                justify-content: center;
            }
        }
    }

    &--product {
        
    }
}


.sidebar-categories {
    &__title {
        font-size: 2.0rem;
        font-weight: $font-weight-medium;
    }

    &__item {
        position: relative;
        padding-right: 40px;

        &:not(:last-child) {
            margin-bottom: 8px;
        }

        &.is-active {
            font-weight: $font-weight-medium;
        }
    }

    &__amount {
        position: absolute;
        right: 0;
        top: 0;
    }

    &__anchor {
        text-decoration: none;
    }
}

.shop-filters {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__count, &__order {
        margin-bottom: 16px;
    }

    &__count {
        margin-right: 20px;
        color: $color-gray;
    }

    &__mobile-toggle {
        display: none;
        position: relative;
        padding: 4px 0 4px 28px;
        margin-bottom: 16px;

        .icon {
            position: absolute;
            top: calc(50% - 10px);
            left: 0;
            width: 20px;
            height: 20px;
        }
    }

    @include below-mobile {
        &__count {
            display: none;
        }

        &__mobile-toggle {
            display: block;
        }
    }

    @include below-mobile-sm {
        flex-direction: column;
        justify-content: center;

        &__order {
            width: 100%;
        }
    }
}

.nutritionist-cta {
    position: relative;
    width: 100%;
    padding: 60px 80px 80px 80px;
    background: $color02-light;
    text-align: center;

    &__title {
        margin: 0 0 8px 0;
    }

    &__text {
        margin: 0;
    }

    &__actions {
        position: absolute;
        bottom: -36px;
        left: 0; right: 0;
        text-align: center;
    }

    @include below-mobile {
        padding: 60px 40px 70px 40px;
    }

    @include below-mobile-sm {
        padding: 40px 40px 50px 40px;

        &__actions {
            margin-top: 20px;
            position: relative;
            bottom: 0;
        }
    }

    @include below-mobile-xs {
        padding: 40px 20px;
    }
}


.faq {
    &__title {
        text-align: center;
        margin-bottom: 40px;
    }
}


.related-products {
    &__title {
        text-align: center;
        margin-bottom: 40px;
    }
}


.product-quantity {
    display: flex;

    .qty {
        display: inline-block;
        margin: 0;
        max-width: 100px;
        min-width: 100px;
    }

    &__button {
        display: inline-block;
        padding: 8px;
        border: 2px solid $color-border02;
        font-size: 3.2rem;
        width: 42px;
        min-height: 60px;
        line-height: 1;
        transition: $transition-default;
        
        &:first-child {
            border-right: 0;
        }
        &:last-child {
            border-left: 0;
        }

        &:hover {
            color: $color-text;
            background: $color-border02;
            border: 2px solid $color-border02;
            transition: $transition-default;

            &:first-child {
                border-right: 0;
            }
            &:last-child {
                border-left: 0;
            }
        }
    }
}


.product-cart {
    display: flex;
    align-items: center;
    margin: 0 -8px;

    .single_add_to_cart_button {
        min-height: 60px;
    }

    &__quantity, &__options {
        padding: 0 8px;
    }

    &__button {
        display: block;
    }

    @include below-desktop {
        flex-wrap: wrap;
        
        &__quantity, &__button {
            margin-bottom: 8px;
        }
    }

    @include below-mobile {
        &__quantity, &__button {
            width: 100%;
        }

        .quantity {
            width: 100%;
        }

        .qty {
            flex: 1;
            max-width: 100%;
        }

        &__options {
            width: 100%;
        }
    }
}


.message-bar {
    position: relative;
    width: 100%;
    background: $color02-light;
    font-weight: $font-weight-medium;
    font-size: 1.6rem;
    padding: 17px 24px 20px 24px;
    min-height: 60px;

    &:before {
        content: "";
        position: absolute;
        left: 30px;
        top: -10px;
        width: 20px;
        height: 20px;
        background: $color02-light;
        transform: rotate(45deg);
    }
}


.product-info {
    &__nav {
        position: relative;
        z-index: 1;
    }

    &__content {
        position: relative;
        padding: 40px;
        border: 2px solid $color-border02;
        border-top: none;
        background: $color-white; //$color02-light;
    }

    @include below-mobile {
        &__nav {
            display: none;
        }

        &__content {
            border: none;
            padding: 0;
        }
    }
}


.article-block {
    display: flex;
    background: $color02-light;

    &__image {
        min-width: 35%;
        background-color: $color01;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
    }

    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 400px;
        padding: 80px 60px 90px 60px;
    }

    &__title, &__content {
        margin-bottom: 30px;
    }

    &__title {
        font-size: 2.8rem;
        text-transform: uppercase;
    }

    @include below-desktop-lg {
        &__inner {
            padding: 60px 40px 70px 40px;
        }
    }

    @include range-tablet {
        flex-direction: column;

        &__inner {
            padding: 45px 40px 65px 40px;
            min-height: 240px;
            justify-content: flex-start;
        }

        &__image {
            height: 240px;
        }
    }

    @include below-desktop {
        &__title {
            font-size: $font-size-h4;
            margin-bottom: 24px;
        }
    }

    @include below-mobile-sm {
        flex-direction: column;

        &__inner {
            padding: 35px 30px 55px 30px;
            min-height: 200px;
            justify-content: flex-start;
        }

        &__image {
            height: 200px;
        }
    }
}


.sidebar-page-nav {
    background: $color02-light;

    &__title {
        padding: 30px;
        font-weight: $font-weight-medium;
        text-transform: uppercase;
        border-bottom: 2px solid $color02;
        letter-spacing: 1px;
    }

    @include below-mobile {
        background: $color-white;

        &__title {
            text-transform: none;
            font-size: 2.0rem;
            border-color: $color-border;
        }
    }
}


.endorsement {
    background-color: $color02-light;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right center;

    &__inner {
        display: flex;
        min-height: 380px;
    }

    &__content {
        flex: 1;
        display: flex;
        align-items: center;
        padding: 60px 40px 70px 40px;
    }

    &__quote {
        margin: 0;
        background: transparent;
        max-width: 480px;
        font-size: $font-size-md;
        font-weight: $font-weight-medium;

        CITE {
            display: block;
            margin-top: 10px;
            font-weight: $font-weight-normal;
            font-size: $font-size-base;
        }
    }

    &__image {
        min-width: 35%;
        background-color: $color01;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
    }


    @include below-desktop-lg {
        &__content {
            padding: 60px 40px 70px 40px;
        }
    }

    @include range-tablet {
        flex-direction: column;

        &__inner {
            flex-direction: column-reverse;
        }

        &__image {
            height: 240px;
        }

        &__content {
            padding: 45px 40px 65px 40px;
        }
    }

    @include below-mobile {
        &__content {
            padding: 40px 30px 60px 30px;
        }

        &__quote {
            padding: 0 0 0 50px;
        }
    }

    @include below-mobile-sm {
        flex-direction: column;

        &__inner {
            flex-direction: column-reverse;
        }

        &__content {
            padding: 35px 30px 55px 30px;
            min-height: 200px;
        }

        &__quote {
            font-size: 2.0rem;
            padding: 45px 0 0 0;

            CITE {
                font-size: 1.6rem;
            }
        }

        &__image {
            height: 200px;
        }
    }
}


.sidebar-featured-products {
    &__title {
        font-weight: $font-weight-medium;
        padding-bottom: 8px;
        border-bottom: 2px solid $color-border;
        margin-bottom: 40px;
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    @include below-mobile {
        &__items {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        &__title {
            font-size: 2.8rem;
            margin-bottom: 40px;
            text-align: center;
            border-bottom: 0;
            padding-bottom: 0;
        }
    }
}


.search-query {
    color: $color-warning;
}


.product-quality {
    padding: 60px 0 30px 0;

    &__inner {
        position: relative;
    }

    &__title {
        margin-bottom: 20px;
        text-align: center;
    }

    &__description {
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
    }

    &__image {
        position: absolute;
        left: 0; right: 0; top: 0; bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
    }

    &__img {
        height: 400px;
        width: 400px;
        margin-bottom: 20px;
    }

    &__content {
        display: flex;
        justify-content: space-between;
    }

    &__left {
        flex: 1;
        text-align: right;
        padding-right: 230px;
    }

    &__right {
        flex: 1;
        text-align: left;
        padding-left: 230px;
    }

    &__col {
        margin-bottom: 60px;
    }

    &__subtitle {
        font-size: $font-size-md;
        font-weight: $font-weight-medium;
        text-transform: uppercase;
        padding-bottom: 4px;
        border-bottom: 2px solid $color-border;
        margin-bottom: 20px;
    }

    @include below-desktop-lg {
        &__img {
            height: 300px;
            width: 300px;
        }

        &__left {
            padding-right: 180px;
        }

        &__right {
            padding-left: 180px;
        }
    }

    @include below-desktop {
        &__img {
            height: 240px;
            width: 240px;
        }

        &__left {
            padding-right: 140px;
        }

        &__right {
            padding-left: 140px;
        }
    }

    @include below-mobile {
        padding: 0 0 20px 0;

        &__image {
            position: relative;
            margin-bottom: 20px;
        }

        &__img {
            width: 280px;
            height: 280px;
        }

        &__content {
            flex-direction: column;
            justify-content: center;
        }

        &__left, &__right {
            padding-left: 0;
            padding-right: 0;
            text-align: center;
        }

        &__subtitle {
            border-bottom: none;
            padding-bottom: 0;
        }
    }
}


.product-dosage {
    display: flex;
    padding: 40px 20px;
    margin: 0 -40px;

    &__col {
        padding: 0 40px;

        &:nth-child(1) {
            flex: 2;
        }

        &:nth-child(2) {
            flex: 3;
        }
    }

    &__box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: $color02-light;
        padding: 60px 40px;
        height: 100%;
        text-align: center;
    }

    &__icon {
        height: 64px;
        width: 64px;
        margin-bottom: 8px;
    }

    &__box-title {
        font-size: $font-size-md;
        font-weight: $font-weight-medium;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__text {
        margin-top: 8px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
    }

    &__block {  
        margin-bottom: 30px;

        &:not(:last-child) {
            padding-bottom: 30px;
            border-bottom: 2px solid $color-border;
        }
    }

    &__title {
        margin-bottom: 20px;
    }

    @include below-desktop-lg {
        margin: 0 -20px;

        &__col {
            padding: 0 20px;

            &:nth-child(1), &:nth-child(2) {
                flex: 1;
            }
        }

        &__content {
            text-align: center;
        }
    }

    @include below-desktop {
        padding: 40px 0;

        &__box {
            padding: 60px 30px;
        }
    }

    @include below-mobile {
        flex-direction: column-reverse;
        margin: 0;
        padding: 20px 0;

        &__col {
            padding: 0;

            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }
    }
}


.product-composition {
    text-align: center;
    padding: 60px 60px 70px 60px;

    &__title {
        margin-bottom: 20px,
    }

    &__subtitle {
        margin: 0;
    }

    &__table {
        margin: 30px 0 0 0;
    }

    &__bottom {
        margin-top: 30px;
    }

    @include below-mobile {
        padding: 40px 0 60px 0;
    }
}


.product-benefits {
    padding: 60px 20px 70px 20px;
    text-align: center;

    &__items {
        display: flex;
        margin: 60px -20px;
    }

    &__item {
        flex: 1;
        padding: 0 20px;
        font-size: 2.0rem;
        font-weight: $font-weight-medium;
        text-transform: uppercase;
    }

    &__title {
        margin-bottom: 20px,
    }

    &__subtitle {
        margin: 0;
    }

    &__actions {
        margin-top: 40px;
    }

    @include below-desktop {
        padding: 60px 0 70px 0;
    }

    @include below-mobile {
        padding: 40px 0 60px 0;

        &__items {
            flex-direction: column;
            margin: 40px 0;
        }

        &__item {
            padding: 0;

            &:not(:last-child) {
                margin-bottom: 40px;
            } 
        }
    }
}

.bundle-benefits {
    padding: 60px 20px 0px 20px;
    text-align: center;

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 60px -20px;
    }

    &__item {
        flex: 1;
        max-width: 380px;
        padding: 0 20px;
        margin: 0 35px;
        font-size: 2.0rem;
        font-weight: $font-weight-medium;
        justify-content: center;

        IMG {
            display: inline;
            margin-bottom: 20px;
        }
    }

    &__heading {
        margin-bottom: 20px;
        font-size: $font-size-md;
        border-bottom: 2px solid $color-gray-lightest;
    }

    &__text {
        font-size: $font-size-base;
        font-weight: $font-weight-normal;
    }
}

.region-settings {
    min-width: 360px;

    &__label {
        font-weight: $font-weight-medium;
    }

    &__currency {
        margin-top: 16px;
        color: $color-gray-light;
        line-height: 1;
    }

    @include below-mobile-sm {
        min-width: 280px;
    }
}


.region-toggle {
    position: relative;

    &__flag {
        position: absolute;
        top: -4px;
        left: 14px;
        width: 18px;
        height: 18px;
        padding: 3px;
        background: $color-white;
        box-shadow: $shadow02;
        pointer-events: none;
    }

    &__img {
        line-height: 0;
        display: block;
    }

    &--mobile {
        .region-toggle__flag {
            left: inherit;
            top: -6px;
            right: 6px;
            z-index: 3;
            pointer-events: none;

            @include below-mobile-sm {
                right: -4px;
            }
        }
    }
}


.related-posts {
    padding: 60px 0 20px 0;

    &__title {
        margin-bottom: 40px;
    }

    @include below-mobile {
        padding-top: 0;

        &__title {
            text-align: center;
