/******************************************************************
LOADER STYLES
Styles for loaders, progress bars etc
******************************************************************/


.loader {
	display: block;
    width: 100%;
    margin: 60px auto;
    text-align: center;

    &__inner {
        display: inline-block;
        position: relative;
        height: 10px;
        width: 50px;
    }

    &__dot {
        position: absolute;
        top: 0;
        background: $color01;
        height: 10px;
        width: 10px;
        font-size: 0;
        border-radius: 10px;
        animation-fill-mode: both;
        animation: loader 1.8s infinite $bezier01;

        &:nth-child(1){
            left: 0;
            animation-delay: 0s;
        }

        &:nth-child(2){
            left: 20px;
            animation-delay: 0.2s;
        }

        &:nth-child(3){
            left: 40px;
            animation-delay: 0.4s;
        }
    }

    &--disabled {
        .loader__dot {
            background: $color-gray;
            animation: none;
        }
    }
}