/******************************************************************
ACCORDION
Styles for accordion components
******************************************************************/


.accordion-content {
    height: 0;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear, opacity 0s linear;

    &.is-active {
        height: auto;
        opacity: 1;
        visibility: visible;
        transition: visibility $trans-time linear, opacity $trans-time linear;
    }
}