.fancybox-container {
    .fancybox-bg {
        background: $color-white;
    }

    .fancybox-content {
        background: transparent;
    }
    
    .fancybox-button {
        background: $color01;
        color: $color-white;
        transition: $transition-default !important;

        &:hover {
            color: $color-white;
            background: $color01-light;
            transition: $transition-default !important;
        }
    }

    .fancybox-button, .fancybox-navigation .fancybox-button {
        width: 60px;
        height: 60px;
        padding: 0;
    }

    .fancybox-button, .fancybox-button > div {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .fancybox-button svg {
        max-width: 32px;
        max-height: 32px;
    }

    .fancybox-button[disabled] {
        color: $color-white;
        background: rgba($color01-light, $opacity-md);

        &:hover {
            color: $color-white;
            background: rgba($color01-light, $opacity-md);
        }
    }

    .fancybox-toolbar {
        .fancybox-button--play, .fancybox-button--thumbs, .fancybox-button--zoom {
            display: none !important;
        }
    }
}