/******************************************************************
DROPDOWN STYLES
Styles for dropdown components
******************************************************************/


.dropdown-content {
    opacity: 0;
    visibility: hidden;
    transition: visibility $trans-time-sm linear, opacity $trans-time-sm linear;

    &.is-active {
        opacity: 1;
        visibility: visible;
        transition: visibility $trans-time-sm linear, opacity $trans-time-sm linear;
    }
}