/******************************************************************
FOOTER STYLES
Styles specific to the site footer
******************************************************************/

.footer {
    &__main {
        display: flex;
        justify-content: space-between;
        border-top: 2px solid $color-border;
        padding: 28px 0 60px 0;
    }

    &__assurances {
        margin-top: 40px;
        border-top: 2px solid $color-border;
        padding: 60px 0 0 0;
    }

    &__assurances-items {
        padding: 0 80px;
    }

    &__copyright {
        font-weight: $font-weight-medium;
        margin-right: 40px;
    }

    @include below-desktop-lg {
        &__assurances {
            padding: 60px 0 0 0;
        }

        &__assurances-items {
            padding: 0;
        }
    }

    @include below-mobile {
        &__main {
            flex-direction: column-reverse;
            align-items: center;
            padding-bottom: 40px;
        }

        &__nav {
            margin-bottom: 20px;

            .menu {
                justify-content: center;
            }
        }

        &__copyright {
            margin-right: 0;
        }
    }

    @include below-mobile-sm {
        &__nav {
            .menu {
                flex-direction: column;
                align-items: center;
            }
        }
    }
}

.assurances {
    display: flex;
    justify-content: center;
    margin: 0 -40px;

    &__item {
        flex: 1;
        padding: 0 40px;
        margin-bottom: 60px;
        text-align: center;
    }

    &__icon {
        width: 36px;
        height: 36px;
        margin-bottom: 16px;
    }

    &__icon--lg {
        width: 48px;
    }

    &__title {
        font-size: $font-size-md;
        text-transform: uppercase;
        margin-bottom: 16px;
    }

    &__text {
        margin: 0;
    }

    @include below-desktop {
        margin: 0 -20px;

        &__item {
            padding: 0 20px;
        }
    }

    @include below-mobile {
        flex-direction: column;
    }
}