/******************************************************************
NAVIGATION STYLES
Styles for various navigation components used throughout the site
******************************************************************/

.inline-nav {
    .menu {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -30px;

        &__item {
            padding: 0 30px;
        }

        &__anchor {
            text-decoration: none;
        }
    }

    @include below-desktop {
        .menu {
            margin: 0 -20px;

            &__item {
                padding: 0 20px;
            }
        }
    }
}


.sidebar-nav {
    .menu {
        &__anchor {
            display: block;
            position: relative;
            text-decoration: none;
            padding: 16px 30px 17px 30px; //20px 30px;

            &.is-active {
                font-weight: $font-weight-medium;
            }
        }

        &__sub-menu {
            height: 0;
            opacity: 0;
            padding: 0 30px;
            visibility: hidden;
            transition: visibility 0s linear, opacity 0s linear;
        
            background: $color02;
            text-align: right;

            .menu__item {
                &:not(:last-child) {
                    margin-bottom: 16px;
                }
            }

            .menu__anchor {
                display: inline-block;
                padding: 0;
            }

            &.is-active {
                height: auto;
                opacity: 1;
                padding: 24px 30px;
                visibility: visible;
                transition: visibility $trans-time linear, opacity $trans-time linear;
            }
        }

        > .menu__item {
            > .menu__anchor {
                color: $color-text;
                transition: $transition-default;

                &:hover {
                    color: $color-text;
                    background: rgba($color02-lighter, $opacity-md);
                    transition: $transition-default;
                }
            }

            &.is-active {
                > .menu__anchor {
                    font-weight: $font-weight-bold;
                }
            }
        }

        &__item {
            &:not(:last-child) {
                border-bottom: 2px solid $color02;
            }

            &--has-children {
                > .menu__anchor {
                    padding-right: 60px;

                    &:after {
                        content: "";
                        position: absolute;
                        top: calc(50% - 7px);
                        right: 30px;
                        background-image: url('../../assets/icons/icon-caron-down.svg');
                        background-repeat: no-repeat;
                        background-size: contain;
                        height: 14px;
                        width: 14px;
                    }
                }
            }
        }
    }

    @include below-mobile {
        .menu {
            &__sub-menu {
                background: $color-border;
            }

            &__item {
                &:not(:last-child) {
                    border-bottom: 2px solid $color-border;
                }
            }

            > .menu__item {
                > .menu__anchor {
                    &:hover {
                        background: $color-white;
                    }
                }
            }
        }
    }
}


.mobile-account-nav {
    margin-bottom: 40px;
    
    SELECT {
        max-width: 100%;
    }
}